import moment from 'moment'
import $deleteModal from '@/components/delete-modal'
import { get } from 'lodash'

export default {
  computed: {
    $user () {
      return this.$store.getters.user
    }
  },

  methods: {
    formatDate (date) {
      if (!date) return '-'
      return moment(date, 'X').format('DD/MM/YYYY')
    },

    formatDateToISO (date) {
      if (!date) return '-'
      return moment(date, 'X').format('YYYY-MM-DD')
    },

    formatDateTime (date) {
      if (!date) return '-'

      return moment(date, 'X').format('DD/MM/YYYY HH:mm')
    },

    formatMoney (n) {
      return new Intl.NumberFormat('fr-FR', {
        minimumFractionDigits: 2
      }).format(n)
    },

    $is (role) {
      if (!Object.keys(this.$user).length) return false

      if (Array.isArray(role)) {
        return this.$isOneOf(role)
      }

      if (typeof role === 'string') {
        return this.$user.roles.includes(role)
      }

      return false
    },

    $isOneOf (roles) {
      if (!Object.keys(this.$user).length) return false

      for (let index = 0; index < roles.length; index++) {
        const role = roles[index]

        if (this.$user.roles.includes(role)) return true
      }

      return false
    },

    $get: get
  },

  created () {
    this.$deleteModal = $deleteModal
  }
}



