<template>
  <mu-dialog
    :title="`Dossier: V-${dossier._id} - ${$get(dossier, 'pnr.new') || $get(dossier, 'pnr.original')}`"
    :open="open"
    @close="close"
  >
    <div style="padding: 1rem 0">
      <div class="buttons-box">
        <mu-raised-button :disabled="disabled" primary label="Confirm" @click="confirmBooking" />
        <mu-raised-button :disabled="disabled" secondary label="Cancel" @click="cancelBooking" />
        <mu-raised-button :disabled="disabled" secondary label="Void" @click="voidBooking" />
        <!-- <mu-raised-button :disabled="disabled" secondary label="Annuler" @click="cancelBooking" /> -->
      </div>
    </div>

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
        />
      </div>
    </footer>

    <mu-dialog :open="dialog" title="Status" @close="closeDialog">
      <ul style="padding: 1rem 0">
        <li v-for="(info, key) in dialogInfo" :key="key">{{ key }}: {{ info }}</li>
      </ul>
    </mu-dialog>
  </mu-dialog>
</template>

<script>
import CONFIRM_BOOKING_VOL from '@/graphql/mutations/confirmBookingVol.gql'
import CANCEL_BOOKING_VOL from '@/graphql/mutations/cancelBookingVol.gql'
import VOID_BOOKING_VOL from '@/graphql/mutations/voidBookingVol.gql'

export default {
  name: 'edit-vol-dialog',
  props: ['open', 'dossier', 'variables'],

  data () {
    return {
      dialog: false,
      dialogInfo: '',
      disabled: false
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    closeDialog () {
      this.dialog = false
    },

    async confirmBooking () {
      const pnr = this.dossier.pnr.original

      try {
        this.disabled = true

        await this.$apollo.mutate({
          mutation: CONFIRM_BOOKING_VOL,
          variables: { pnr },
          update: (store, { data: { confirmBookingVol } }) => {
            this.showResponse(confirmBookingVol)
            this.disabled = false
          }
        })
      } catch (error) {
        this.disabled = false
        this.showResponse(error)
        console.error(error)
      }
    },

    async cancelBooking () {
      const pnr = this.dossier.pnr.original

      try {
        this.disabled = true

        await this.$apollo.mutate({
          mutation: CANCEL_BOOKING_VOL,
          variables: { pnr },
          update: (store, { data: { cancelBookingVol } }) => {
            this.showResponse(cancelBookingVol)
            this.disabled = false
          }
        })
      } catch (error) {
        this.disabled = false
        this.showResponse(error)
        console.error(error)
      }
    },

    async voidBooking () {
      const pnr = this.dossier.pnr.original

      try {
        this.disabled = true

        await this.$apollo.mutate({
          mutation: VOID_BOOKING_VOL,
          variables: { pnr },
          update: (store, { data: { voidBookingVol } }) => {
            this.showResponse(voidBookingVol)
            this.disabled = false
          }
        })
      } catch (error) {
        this.disabled = false
        this.showResponse(error)
        console.error(error)
      }
    },

    showResponse (response) {
      this.dialog = true
      this.dialogInfo = response
    }
  }
}
</script>

<style lang="scss" scoped>
.buttons-box {
  padding-top: 1rem;
}
</style>



