<template>
  <mu-drawer :open="open" :zDepth="1" :docked="docked" @close="handleClose" id="navbar" v-if="Object.keys($user).length">
    <mu-list @change="handleMenuChange" :val="menuVal">
      <!-- Home -->
      <mu-list-item title="Accueil" value="home" :to="{ name: 'Home' }">
        <mu-icon slot="left" value="home"></mu-icon>
      </mu-list-item>

      <!-- Dossiers -->
      <mu-list-item title="Dossiers" toggleNested :open="isOpenMenu('dossiers')">
        <mu-icon slot="left" value="folder_open"/>

        <!-- Vols -->
        <mu-list-item
          v-if="$can('read', 'vols')"
          slot="nested" title="Vols"
          value="dossiers/vols"
          :to="{ name: 'Vols' }"
        >
          <mu-icon
            slot="left"
            value="flight"
            style="transform: rotate(45deg); font-weight: bold"
          ></mu-icon>
        </mu-list-item>

        <!-- Hotels -->
        <mu-list-item
          v-if="$can('read', 'hotels')"
          slot="nested" title="Hotels"
          value="dossiers/hotels"
          :to="{ name: 'Hotels' }"
        >
          <mu-icon
            slot="left"
            value="hotel"
          ></mu-icon>
        </mu-list-item>

        <!-- Visas -->
        <mu-list-item
          v-if="$can('read', 'visas')"
          slot="nested" title="Visas"
          value="dossiers/visas"
          :to="{ name: 'Visas' }"
        >
          <mu-icon
            slot="left"
            value="insert_drive_file"
          ></mu-icon>
        </mu-list-item>

        <!-- Telex -->
        <mu-list-item
          v-if="$can('read', 'telex')"
          slot="nested" title="Telex"
          value="dossiers/telex"
          :to="{ name: 'Telex' }"
        >
          <mu-icon
            slot="left"
            class="mdi mdi-icons mdi-file-document"
            value="null"
          ></mu-icon>
        </mu-list-item>

        <!-- Assurances -->
        <mu-list-item
          v-if="$can('read', 'assurances')"
          slot="nested" title="Assurances"
          value="dossiers/assurances"
          :to="{ name: 'Assurances' }"
        >
          <mu-icon
            slot="left"
            value="insert_drive_file"
          ></mu-icon>
        </mu-list-item>

        <!-- Transactions -->
        <mu-list-item
          v-if="$can('read', 'transactions')"
          slot="nested"
          title="Livraisons"
          value="dossiers/transactions"
          :to="{ name: 'Transactions' }"
        >
          <mu-icon slot="left" value="null" class="mdi mdi-icons mdi-truck-fast"></mu-icon>
        </mu-list-item>
      </mu-list-item>

      <!-- Réglages -->
      <mu-list-item
        title="Réglages"
        v-if="$is('admin')"
        toggleNested
        :open="isOpenMenu('settings')"
      >
        <mu-icon slot="left" value="settings"/>

        <!-- Users -->
        <mu-list-item
          v-if="$can('read', 'users')"
          slot="nested"
          title="Utilisateurs"
          value="settings/users"
          :to="{ name: 'Users' }"
        >
          <mu-icon slot="left" value="person"></mu-icon>
        </mu-list-item>

        <!-- Roles -->
        <mu-list-item
          v-if="$can('read', 'roles')"
          slot="nested"
          title="Roles"
          value="settings/roles"
          :to="{ name: 'Roles' }"
        >
          <mu-icon slot="left" value="people"></mu-icon>
        </mu-list-item>
      </mu-list-item>

      <mu-list-item
        title="Commissions"
        v-if="$is('admin')"
        toggleNested
        :open="isOpenMenu('commissions')"
      >
        <mu-icon slot="left" value="monetization_on"/>

        <!-- Commissions -->
        <mu-list-item
          v-if="$can('read', 'commissions')"
          slot="nested"
          title="Commissions"
          value="commissions/commissions"
          :to="{ name: 'Commissions' }"
        >
          <mu-icon
            slot="left"
            value="monetization_on"
          ></mu-icon>
        </mu-list-item>

        <!-- Prix visas -->
        <mu-list-item
          v-if="$can('read', 'prix-visas')"
          slot="nested"
          title="Prix visas"
          value="commissions/visas-prices"
          :to="{ name: 'VisasPrices' }"
        >
          <mu-icon
            slot="left"
            value="monetization_on"
          ></mu-icon>
        </mu-list-item>

        <!-- Visas deposits -->
        <mu-list-item
          v-if="$can('read', 'prix-visas')"
          slot="nested"
          title="Deposits visas"
          value="commissions/visas-deposits"
          :to="{ name: 'VisasDeposits' }"
        >
          <mu-icon
            slot="left"
            value="monetization_on"
          ></mu-icon>
        </mu-list-item>
      </mu-list-item>

      <!-- Website -->
      <mu-list-item
        title="Site"
        v-if="$is('admin')"
        toggleNested
        :open="isOpenMenu('site')"
      >
        <mu-icon slot="left" value="web"/>

        <!-- Destinatons -->
        <mu-list-item
          v-if="$can('read', 'destinations')"
          slot="nested"
          title="Destinations"
          value="site/destinations"
          :to="{ name: 'Destinations' }"
        >
          <mu-icon
            slot="left"
            value="location_city"
          ></mu-icon>
        </mu-list-item>
      </mu-list-item>

      <!-- Livraison -->
      <mu-list-item
        title="Livraison"
        v-if="$is(['admin', 'gestionnaire'])"
        toggleNested
        :open="isOpenMenu('delivery')"
      >
        <mu-icon slot="left" value="place"/>

        <!-- Delivery zones -->
        <mu-list-item
          slot="nested"
          title="Zones livraisons"
          value="delivery/zones"
          :to="{ name: 'DeliveryZones' }"
        >
          <mu-icon slot="left" value="local_shipping"></mu-icon>
        </mu-list-item>

        <!-- Wilayas -->
        <mu-list-item
          slot="nested"
          title="Wilayas"
          value="delivery/wilayas"
          :to="{ name: 'Wilayas' }"
        >
          <mu-icon slot="left" value="add_location"></mu-icon>
        </mu-list-item>
      </mu-list-item>
    </mu-list>
  </mu-drawer>
</template>

<script>
export default {
  props: ['open', 'docked'],
  data () {
    return {
      menuVal: '/'
    }
  },

  methods: {
    handleClose () {
      this.$emit('close')
    },

    handleMenuChange (val) {
      this.menuVal = val
      this.$emit('change')
    },

    handleHide () {
      if (!this.changeHref) return
      window.location.hash = this.menuVal
      this.changeHref = false
    },

    isOpenMenu (name) {
      return this.menuVal.split('/')[0] === name
    }
  },

  created () {
    const url = this.$route.path.slice(1)
    this.menuVal = url
  }
}
</script>

<style lang="scss" scoped>
  #navbar {
    margin-top: 64px;

    @media (max-width: 480px) {
      margin-top: 56px;
    }
  }
</style>



