<template>
  <mu-dialog :open="open" @close="close" title="Role">
    <div>
      <!-- Nom -->
      <mu-text-field
        v-model="role.name"
        label="Nom"
        labelFloat
        disabled
      />

      <!-- Resources -->
      <template v-if="open && resources && role">
        <div
          v-for="(permissions, resource) in resources"
          :key="resource"
        >
          <mu-select-field
            :label="resource"
            multiple
            v-model="rolePermissions[resource]"
          >
            <mu-menu-item
              v-for="(permission, index) in permissions"
              :key="index"
              :value="permission"
              :title="permission"
            />
          </mu-select-field>
        </div>
      </template>
    </div>

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
        />

        <!-- Button sauvegarder -->
        <mu-raised-button
          label="Sauvegarder"
          @click.prevent="save"
          style="margin: 0 1rem"
          primary
        />
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
import { cloneDeep, findKey } from 'lodash'
import ROLES_QUERY from '@/graphql/queries/rolesQuery.gql'
import EDIT_ROLE from '@/graphql/mutations/editRole.gql'

export default {
  name: 'edit-role',
  props: ['open', 'data', 'resources'],

  data () {
    return {
      role: {},
      rolePermissions: {}
    }
  },

  watch: {
    open (value) {
      if (value) {
        this.role = cloneDeep(this.data)
        let perms = cloneDeep(this.resources)

        for (let resource in perms) {
          perms[resource] = []
        }

        this.rolePermissions = {
          ...perms,
          ...this.role.permissions
        }
      }
    },

    data (value) {
      this.role = cloneDeep(value)
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    async save () {
      await this.editRole()
      this.close()
    },

    cleanPermissionsArray () {
      for (let index in this.rolePermissions) {
        if (!this.rolePermissions[index].length) {
          delete this.rolePermissions[index]
        }
      }
    },

    async editRole () {
      this.cleanPermissionsArray()
      this.role.permissions = this.rolePermissions

      try {
        await this.$apollo.mutate({
          mutation: EDIT_ROLE,
          variables: {
            ...this.role
          },
          update: (store, { data: { updateRole } }) => {
            let data = store.readQuery({ query: ROLES_QUERY })
            // Get user index
            const indexRole = findKey(data.roles, {
              name: this.role.name
            })

            if (indexRole) {
              data.roles[indexRole] = updateRole
            } else {
              data.roles.push(updateRole)
            }

            store.writeQuery({
              query: ROLES_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>



