<template>
  <section class="hero is-fullheight is-info">
    <div class="hero-body">
      <div class="container has-text-centered">
        <form class="login has-text-left" @submit.prevent="login">
          <div class="has-text-centered">
            <h1 class="title has-text-grey-dark" style="font-size: 3.5rem; margin: 0">Votel</h1>
            <h2 class="title has-text-grey-dark" style="font-size: 1.7rem">Réservation</h2>
          </div>

          <!-- Error message -->
          <p class="has-text-danger" v-if="isShownError" style="padding: 1.5rem auto">
            Erreurs: {{ error.graphQLErrors[0].name }}
            <br/>
            Message: {{ error.graphQLErrors[0].message }}
          </p>

          <!-- Username field -->
          <mu-text-field
            label="Nom d'utilisateur ou email"
            v-model="email"
            icon="person"
            labelFloat
            @input="hideError"
            @keyup.native.enter="login"
          />
          <br/>

          <!-- Password field -->
          <mu-text-field
            label="Mot de passe"
            v-model="password"
            type="password"
            icon="lock"
            labelFloat
            @input="hideError"
            @keyup.native.enter="login"
          />

          <br/>

          <mu-switch label="Se souvenir de moi" v-model="rememberme" style="padding: 1rem"/>

          <br>

          <!-- Login button -->
          <mu-raised-button
            style="margin-top: 1.5rem"
            label="Connexion"
            primary
            fullWidth
            :disabled="disableLogin"
            @click.prevent="login"
          />
        </form>
      </div>
    </div>
  </section>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import LOGIN_QUERY from '@/graphql/queries/loginQuery.graphql'

export default {
  name: 'login',

  data () {
    return {
      email: '',
      password: '',
      rememberme: false,
      disableLogin: false,
      isShownError: false,
      error: {}
    }
  },

  computed: {
    ...mapGetters(['isLoggedIn'])
  },

  methods: {
    ...mapActions(['setLoggedIn', 'setUser']),
    async login () {
      this.toggleLoginButton(true)
      try {
        const { data: { login } } = await this.$apollo.query({
          query: LOGIN_QUERY,
          variables: {
            email: this.email,
            password: this.password,
            rememberme: this.rememberme
          },
          fetchPolicy: 'network-only'
        })

        this.toggleLoginButton(false)
        this.setUser({ user: login })
        const permissions = []
        Object.keys(login.permissions).forEach(resource => {
          permissions.push({ subject: resource, actions: login.permissions[resource] })
        })
        this.$ability.update(permissions)
        this.setLoggedIn()
      } catch (error) {
        this.toggleLoginButton(false)
        this.showError(error)
        console.error(error)
      }
    },

    toggleLoginButton (status) {
      this.disableLogin = status
    },

    showError (error) {
      this.isShownError = true
      this.error = error
    },

    hideError () {
      this.isShownError = false
    }
  },

  mounted () {
    if (this.isLoggedIn) {
      this.$router.push({ name: 'Home' })
    }
  }
}
</script>

<style lang="scss" scoped>
  .hero {
    background-image: url('../assets/login-bg.jpeg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
  }

  .login {
    display: inline-block;
    // background: inherit;
    border-radius: 7px;
    background-color: rgba(#fefefe, .92);
    padding: 4rem 3rem;
    margin: auto;

    .mu-text-field {
      width: 20rem;
    }
  }
</style>



