<template>
  <mu-dialog :open="open" @close="close" v-if="Object.keys(transaction).length">
    <div style="padding: 1rem">
      <mu-switch v-model="background" label="Image de fond"></mu-switch>
    </div>

    <div id="recu">
      <!-- ID -->
      <div id="id">{{ id || '-' }}</div>

      <!-- Name -->
      <div id="name" class="is-uppercase">{{ transaction.address.name || '-' }}</div>

      <!-- Prix -->
      <div id="total">{{ formatMoney(transaction.price.amount) || '-' }}</div>

      <!-- Prix in letters -->
      <div id="total-words" class="is-capitalized">{{ numberToWords(transaction.price.amount) || '-' }} Dinars Algériens</div>
    </div>

    <mu-flat-button class="noprint" label="Imprimer" slot="actions" secondary @click="print()"/>
    <mu-flat-button class="noprint" label="Fermer" slot="actions" primary @click="close"/>
  </mu-dialog>
</template>

<script>
import printJS from 'print-js'
import writtenNumber from 'written-number'

export default {
  name: 'recu',
  props: ['open', 'transaction'],

  data () {
    return {
      background: false
    }
  },

  computed: {
    id () {
      if (this.transaction.vol) return `V-${this.transaction.vol._id}`
      if (this.transaction.hotel) return `H-${this.transaction.hotel._id}`
      if (this.transaction.visa) return `Vi-${this.transaction.visa._id}`
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    print () {
      const recu = document.getElementById('recu')
      if (this.background) {
        recu.classList.add('with-background')
      }

      printJS({
        printable: 'recu',
        type: 'html',
        font: 'Arial',
        honorColor: true,
        targetStyles: ['*'],
        style: `
          @page {
            size: A4;
            margin: 0;
            padding: 0;
          }

          #recu {
            min-height: 148mm !important;
            min-width: 210mm !important;
          }

          #id {
            position: absolute !important;
            top: 5.43cm !important;
            left: 2.15cm !important;
          }

          #name {
            position: absolute !important;
            top: 6.55cm !important;
            left: 5.7cm !important;
          }

          #total {
            position: absolute !important;
            top: 7.65cm !important;
            left: 4.8cm !important;
          }

          #total-words {
            position: absolute !important;
            top: 8.7cm !important;
            left: 3.8cm !important;
          }
        `
      })

      recu.classList.remove('with-background')
    },

    numberToWords (number) {
      return writtenNumber(number, { lang: 'fr' })
    }
  }
}
</script>

<style lang="scss">
    #recu {
    &.with-background {
      background: url('../../assets/recu_paiement.png') no-repeat top left;
      background-size: 100%;
    }

    margin: 1rem;
    padding: 2rem;
    color: #000;
    font-weight: 500;
    line-height: 1cm;

    .panel-block {
      align-items: baseline;
    }

    table {
      border: 1px solid black;
      width: 100%;

      td, th {
        border: 1px solid black;
        padding: .5rem;
      }
    }

    .itinerary {
      padding: .5rem 0;
    }
  }
</style>



