<template>
  <mu-dialog :open="open" @close="close" title="Operateur">
    <div style="min-width: 300px">
      <mu-select-field
        v-model="operator"
        label="Operateur"
        labelFloat
        fullWidth
        v-if="!$apollo.queries.users.loading"
      >
        <mu-menu-item
          v-for="(user, index) in users"
          :key="index"
          :value="user._id"
          :title="user.name"
        />
      </mu-select-field>

      <br/>

      <!-- Footer -->
      <footer>
        <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
          <!-- Button annuler -->
          <mu-raised-button
            secondary label="Fermer"
            @click.prevent="close"
          ></mu-raised-button>

          <!-- Button valider -->
          <mu-raised-button
            primary
            label="Valider"
            style="margin: 0 .5rem"
            @click.prevent="update"
            :loading="loading"
          ></mu-raised-button>
        </div>
      </footer>
    </div>
  </mu-dialog>
</template>

<script>
import USERS_QUERY from '@/graphql/queries/usersQuery.gql'

export default {
  name: 'operator',
  props: ['open', 'dossier', 'type'],

  data () {
    return {
      users: [],
      operator: null,
      loading: false
    }
  },

  apollo: {
    users: {
      query: USERS_QUERY,
      fetchPolicy: 'network-only',
      skip () {
        return !this.open
      }
    }
  },

  watch: {
    open (value) {
      this.operator = null
      if (this.dossier.operator) {
        this.operator = this.dossier.operator._id
      }
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    update () {
      this.loading = true

      switch (this.type) {
        case 'vol':
          this.$emit('update', {
            pnr: this.dossier.pnr.original,
            operator: this.operator
          })
          this.loading = false
          this.close()
          break

        default:
          this.$emit('update', {
            _id: this.dossier._id,
            operator: this.operator
          })
          this.loading = false
          this.close()
          break
      }
    }
  }
}
</script>



