<template>
  <mu-dialog :open="open" @close="close" title="Contacts">
    <!-- Dialog body -->
    <div style="padding-top: 1rem">
      <mu-paper
        class="paper-card"
        v-for="(contact, key) in contacts"
        :key="key"
      >
        <!-- Title bar -->
        <div class="title-bar">
          <h2 style="flex: 9">Contact #{{ key + 1 }}</h2>
          <div style="flex: 1">
            <mu-icon-button
              style="flex: 2; color: #ff4081"
              icon="delete"
              @click="promptRemove(contact, key)"
            />
          </div>
        </div>

        <!-- Contact card -->
        <div class="paper-card-content">
          <!-- Name field -->
          <mu-text-field
            label="Nom"
            v-model="contact.name"
            style="margin: 0 1rem 0 0"
            labelFloat
          />

          <!-- Email field -->
          <mu-text-field
            label="Email"
            type="email"
            v-model="contact.email"
            style="margin: 0 1rem 0 0" labelFloat
          />

          <!-- Address field -->
          <mu-text-field
            label="Adresse"
            v-model="contact.address"
            fullWidth
            multiLine
            :rows="2"
            labelFloat
            style="vertical-align: bottom"
          />

          <!-- Phones fields -->
          <div
            v-if="contact.phones"
            v-for="(phone, key) in contact.phones"
            :key="key"
            class="phone-container"
          >
            <!-- Phone field -->
            <mu-text-field
              style="flex: 8"
              :label="`Telephone #${key + 1}`"
              v-model="phone.number"
              labelFloat
            />

            <!-- Remove phone button -->
            <div style="flex: 1; align-self: flex-end; padding: 1rem 0">
              <mu-float-button
                secondary
                icon="remove"
                style="width: 20px; height: 20px"
                @click="contact.phones.splice(key, 1)"
              />
            </div>
          </div>

          <!-- Contact buttons -->
          <div style="display: flex">
            <!-- Save button -->
            <div style="flex: 9">
              <mu-raised-button primary label="Sauvegarder" :disabled="disabled" @click="save(contact, key)"/>
            </div>

            <!-- Add phone number button -->
            <div style="flex: 1">
              <mu-icon-button
                icon="add"
                @click="addPhone(key)"
              />
            </div>
          </div>
        </div>
      </mu-paper>

      <!-- Add contact button -->
      <div style="margin: 0 0 1rem 0; text-align: right">
        <mu-raised-button label="Ajouter" icon="add" @click="newContact"/>
      </div>
    </div>

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
        />
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
import { cloneDeep, findKey } from 'lodash'
import VOLS_QUERY from '@/graphql/queries/volsQuery.gql'
import HOTELS_QUERY from '@/graphql/queries/hotelsQuery.gql'
import VISAS_QUERY from '@/graphql/queries/visasQuery.gql'
import TELEXES_QUERY from '@/graphql/queries/telexesQuery.gql'
import ASSURANCES_QUERY from '@/graphql/queries/assurancesQuery.gql'
import ADD_CONTACT_MUTATION from '@/graphql/mutations/addContact.gql'
import EDIT_CONTACT_MUTATION from '@/graphql/mutations/editContact.gql'
import DELETE_CONTACT_MUTATION from '@/graphql/mutations/deleteContact.gql'

export default {
  name: 'edit-contacts',
  props: ['open', 'dossier', 'type', 'variables'],

  data () {
    return {
      contacts: [],
      disabled: false
    }
  },

  watch: {
    open (value) {
      if (value) {
        this.contacts = cloneDeep(this.dossier.contacts)
      }
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    promptRemove (contact, index) {
      if (!contact._id) {
        this.contacts.splice(index, 1)
        return
      }

      this.$deleteModal.prompt({
        title: 'Supprimer contact',
        onConfirm: () => {
          this.remove(contact, index)
        }
      })
    },

    remove (contact, index) {
      this.deleteContact(contact._id, index)
    },

    newContact () {
      this.contacts = [...this.contacts, {phones: [{}]}]
    },

    addPhone (index) {
      this.contacts[index].phones.push({})
    },

    async save (params, index) {
      if (params._id) {
        await this.editContact(params, index)
      } else {
        await this.addContact(params, index)
      }
    },

    async addContact (params, index) {
      this.disabled = true
      try {
        await this.$apollo.mutate({
          mutation: ADD_CONTACT_MUTATION,
          variables: {
            dossierInfo: {
              type: this.type,
              id: this.dossier._id
            },
            ...params,
            phones: params.phones.map((phone) => phone.number)
          },
          update: (store, { data: { addContact } }) => {
            let q
            let dossierType

            if (this.type === 'vol') {
              q = VOLS_QUERY
              dossierType = 'vols'
            }

            if (this.type === 'hotel') {
              q = HOTELS_QUERY
              dossierType = 'hotels'
            }

            if (this.type === 'visa') {
              q = VISAS_QUERY
              dossierType = 'visas'
            }

            if (this.type === 'telex') {
              q = TELEXES_QUERY
              dossierType = 'telexes'
            }

            if (this.type === 'assurance') {
              q = ASSURANCES_QUERY
              dossierType = 'assurances'
            }

            let data = store.readQuery({
              query: q,
              variables: { ...this.variables }
            })

            // Get dossier index
            const indexDossier = findKey(data[dossierType].data, {
              _id: this.dossier._id
            })

            data[dossierType].data[indexDossier].contacts.push(addContact)

            store.writeQuery({
              query: q,
              variables: { ...this.variables },
              data
            })

            this.contacts[index] = addContact
            this.disabled = false
          }
        })
      } catch (error) {
        console.error(error)
        this.disabled = false
      }
    },

    async editContact (params, index) {
      this.disabled = true
      try {
        await this.$apollo.mutate({
          mutation: EDIT_CONTACT_MUTATION,
          variables: {
            ...params,
            phones: params.phones.map((phone) => phone.number)
          },
          update: (store, { data: { editContact } }) => {
            let q
            let dossierType

            if (this.type === 'vol') {
              q = VOLS_QUERY
              dossierType = 'vols'
            }

            if (this.type === 'hotel') {
              q = HOTELS_QUERY
              dossierType = 'hotels'
            }

            if (this.type === 'visa') {
              q = VISAS_QUERY
              dossierType = 'visas'
            }

            if (this.type === 'telex') {
              q = TELEXES_QUERY
              dossierType = 'telexes'
            }

            if (this.type === 'assurance') {
              q = ASSURANCES_QUERY
              dossierType = 'assurances'
            }

            let data = store.readQuery({
              query: q,
              variables: { ...this.variables }
            })

            // Get dossier index
            const indexDossier = findKey(data[dossierType].data, {
              _id: this.dossier._id
            })

            // Get updated contact index
            const indexContact = findKey(data[dossierType].data[indexDossier].contacts, { _id: editContact._id })

            data[dossierType].data[indexDossier].contacts[indexContact] = editContact

            store.writeQuery({
              query: q,
              variables: { ...this.variables },
              data
            })

            this.contacts[index] = editContact
            this.disabled = false
          }
        })
      } catch (error) {
        console.error(error)
        this.disabled = false
      }
    },

    async deleteContact (id, index) {
      try {
        await this.$apollo.mutate({
          mutation: DELETE_CONTACT_MUTATION,
          variables: {
            _id: id,
            dossierInfo: {
              type: this.type,
              id: this.dossier._id
            }
          },
          update: (store, response) => {
            let q
            let dossierType

            if (this.type === 'vol') {
              q = VOLS_QUERY
              dossierType = 'vols'
            }

            if (this.type === 'hotel') {
              q = HOTELS_QUERY
              dossierType = 'hotels'
            }

            if (this.type === 'visa') {
              q = VISAS_QUERY
              dossierType = 'visas'
            }

            let data = store.readQuery({
              query: q,
              variables: { ...this.variables }
            })

            // Get dossier index
            const indexDossier = findKey(data[dossierType].data, {
              _id: this.dossier._id
            })

            // Get updated contact index
            const indexContact = findKey(data[dossierType].data[indexDossier].contacts, { _id: id })

            data[dossierType].data[indexDossier].contacts.splice(indexContact, 1)

            store.writeQuery({
              query: q,
              variables: { ...this.variables },
              data
            })

            this.contacts.splice(index, 1)
          }
        })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .paper-card-content {
    padding: 1rem;
    max-width: 600px;
  }

  .title-bar {
    display: flex;
  }

  .phone-container {
    @media screen and (min-width: 596px) {
      width: 50%;
      display: inline-flex;
    }
  }
</style>



