<template>
  <mu-date-picker
    :hintText="hintText"
    okLabel="Ok"
    cancelLabel="Fermer"
    :dateTimeFormat="frDateFormat"
    :firstDayOfWeek="0"
    :label="label"
    labelFloat
    :value="date"
    @input="updateValue"
    container="inline"
    mode="landscape"
    :format="format"
    :disabled="disabled || false"
  />
</template>

<script>
const dayAbbreviation = ['D', 'L', 'M', 'M', 'J', 'V', 'S']
const dayList = ['Dim', 'Lun', 'Mar', 'Mer', 'Jeu', 'Ven', 'Sam']
const monthList = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep',
  'Oct', 'Nov', 'Dec']
const monthLongList = ['Janvier', 'Fevrier', 'Mars', 'Avril', 'Mai', 'Juin',
  'Juillet', 'Aout', 'Septembre', 'Octobre', 'Novembre', 'Decembre']

const frDateFormat = {
  formatDisplay (date) {
    return `${dayList[date.getDay()]}, ${date.getDate()} ${monthList[date.getMonth()]}`
  },
  formatMonth (date) {
    return `${monthLongList[date.getMonth()]} ${date.getFullYear()}`
  },
  getWeekDayArray (firstDayOfWeek) {
    let beforeArray = []
    let afterArray = []
    for (let i = 0; i < dayAbbreviation.length; i++) {
      if (i < firstDayOfWeek) {
        afterArray.push(dayAbbreviation[i])
      } else {
        beforeArray.push(dayAbbreviation[i])
      }
    }
    return beforeArray.concat(afterArray)
  }
}

export default {
  props: {
    hintText: String,
    label: String,
    value: String,
    disabled: Boolean,
    format: {
      type: String,
      default: 'YYYY-MM-DD'
    }
  },

  data () {
    return {
      date: this.value,
      frDateFormat
    }
  },

  watch: {
    value (val) {
      this.date = val
    }
  },

  methods: {
    updateValue (val) {
      this.date = val
      this.$emit('input', val)
    }
  }
}
</script>



