<template>
  <div class="content-wrapper">
    <!-- Search field -->
    <div style="padding-bottom: 1.3rem">
      <mu-text-field
        icon="search"
        hintText="Chercher"
        v-model="searchQuery"
        fullWidth
      ></mu-text-field>
    </div>

    <!-- Tabs -->
    <div class="tabs is-boxed is-centered">
      <ul>
        <li
          v-for="(label, index) in statusVisas"
          :key="index"
          :class="{'is-active': status === index, 'is-disabled': $apollo.queries.visas.loading}"
          @click="changeStatus(index)"
        >
          <a>
            {{ label }}
          </a>
        </li>
      </ul>
    </div>

    <b-table :data="visasFiltered" striped :loading="$apollo.queries.visas.loading">
      <template slot-scope="props">
        <!-- ID -->
        <b-table-column field="_id" label="#">
          {{ props.row._id }}
        </b-table-column>

        <!-- Name -->
        <b-table-column field="name" label="Nom">
          <a @click.prevent="editName(props.row)">{{ props.row.name }}</a>
        </b-table-column>

        <!-- Status -->
        <b-table-column field="stauts" label="Status">
          <a v-if="$can('update', 'visas')" @click.prevent="editStatus(props.row)">
            {{ statusVisas[props.row.status] }}
          </a>

          <span v-else>{{ statusVisas[props.row.status] }}</span>
        </b-table-column>

        <!-- Contact -->
        <b-table-column field="contact" label="Contact" :visible="$can('read', 'contacts')" width="150">
          <a v-if="$can('update', 'contacts')" @click="editContacts(props.row)">
            {{ props.row.contacts.length ? props.row.contacts[0].name : '-' }} :
            <span style="white-space: nowrap">
              {{ $get(props.row, 'contacts[0].phones[0].number', '-') }}
            </span>
          </a>

          <span v-else>
            {{ props.row.contacts.length ? props.row.contacts[0].name : '-' }} :
            <span style="white-space: nowrap">
              {{ $get(props.row, 'contacts[0].phones[0].number', '-') }}
            </span>
          </span>
        </b-table-column>

        <!-- Destination -->
        <b-table-column field="destination" label="Destination">
          <span class="is-uppercase">{{ props.row.destination }}</span>
        </b-table-column>

        <!-- Wilaya -->
        <b-table-column field="wilaya" label="Wilaya">
          {{ $get(props, 'row.wilaya.name', '-') }}
        </b-table-column>

        <!-- Date départ -->
        <b-table-column field="departureDate" label="Date départ">
          {{ formatDate(props.row.departureDate) }}
        </b-table-column>

        <!-- Durée visa -->
        <b-table-column field="duration" label="Durée">
          {{ props.row.duration }}
        </b-table-column>

        <!-- Opérateur -->
        <b-table-column label="Op." :visible="$can('read', 'operateurs')">
          <a
            v-if="$can('update', 'operateurs')"
            @click.prevent="editOperator(props.row)"
          >{{ props.row.operator ? props.row.operator.name : '-' }}</a>

          <span v-else>{{ props.row.operator ? props.row.operator.name : '-' }}</span>
        </b-table-column>

        <!-- Prix -->
        <b-table-column label="Prix">
          <a @click="editTransactions(props.row)">
            <span v-if="props.row.transactions.length">
              {{ formatMoney(props.row.transactions[0].price.amount) }}
            </span>

            <span v-else>-</span>
          </a>
        </b-table-column>

        <!-- Actions -->
        <b-table-column width="190" label="Actions" :visible="!$can('operateurs', 'update')">
          <!-- Self assign dossier -->
          <b-tooltip
            label="S'assigner le dossier"
            position="is-top"
            v-if="!$can('operateurs', 'update')"
          >
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="assignDossier(props.row)"
              :disabled="props.row.operator && $user._id === props.row.operator._id"
            >
              <mu-icon value="check" :size="24" />
            </a>
          </b-tooltip>

          <!-- Edit dossier -->
          <!-- <b-tooltip
            label="Editer dossier"
            position="is-top"
            v-if="!$can('visas', 'update')"
          >
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="editVisa(props.row)"
            >
              <mu-icon value="edit" :size="24" />
            </a>
          </b-tooltip> -->

          <!-- Treatment steps button -->
          <b-tooltip
            label="Status de traitement"
            position="is-top"
            v-if="!$can('visas', 'update')"
          >
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="openTreatmentDialog(props.row)"
            >
              <mu-icon value="settings" :size="24" />
            </a>
          </b-tooltip>

          <!-- Upload passport button -->
          <b-tooltip
            label="Upload fichier passport"
            position="is-top"
            v-if="!$can('visas', 'update')"
          >
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="openPassportDialog(props.row)"
            >
              <mu-icon value="attach_file" :size="24" />
            </a>
          </b-tooltip>
        </b-table-column>
      </template>
    </b-table>

    <!-- Pagination -->
    <b-pagination
      style="padding: 1.5rem 0"
      :total="visas.total"
      :current.sync="page"
      :rounded="true"
      :per-page="pageSize"
      :order="'is-centered'"
    />

    <!-- Edit status dialog -->
    <status
      :open.sync="statusDialog.open"
      :dossier="statusDialog.dossier"
      type="visa"
      @update="updateDossier"
      @no-passport="passportError"
      @no-visa="visaError"
    />

    <!-- Edit operator dialog -->
    <operator
      :open.sync="operatorDialog.open"
      :dossier="operatorDialog.dossier"
      type="visa"
      @update="updateDossier"
    />

    <!-- Edit contacts dialog -->
    <edit-contacts
      :open.sync="contactsDialog.open"
      :dossier="contactsDialog.dossier"
      :variables="variables"
      type="visa"
    />

    <!-- Edit transactions dialog -->
    <edit-transactions
      :open.sync="transactionsDialog.open"
      :dossier="transactionsDialog.dossier"
      :variables="variables"
      :wilayas="wilayas"
      type="visa"
    />

    <!-- Treatment steps dialog -->
    <treatment-steps
      :open.sync="treatmentDialog.open"
      :dossier="treatmentDialog.dossier"
      :done="done"
      @update="updateSteps"
      @update-visa="updateVisa"
    />

    <!-- Passport upload -->
    <passport-upload
      :open.sync="passportUploadDialog.open"
      :dossier="passportUploadDialog.dossier"
      @update="updatePassport"
      :done="done"
    />

    <!-- Edit visa -->
    <edit-visa
      :open.sync="editVisaDialog.open"
      :dossier="editVisaDialog.dossier"
      @update="updateDossier"
    />
  </div>
</template>

<script>
import { find, findKey, orderBy } from 'lodash'

import Status from '@/components/dossiers/Status'
import Operator from '@/components/dossiers/Operator'
import EditContacts from '@/components/edit-contacts'
import EditTransactions from '@/components/transactions/EditTransactions'
import TreatmentSteps from '@/components/visas/TreatmentSteps'
import PassportUpload from '@/components/visas/PassportUpload'
import EditVisa from '@/components/visas/EditVisa'

import VISAS_QUERY from '@/graphql/queries/visasQuery.gql'
import DOSSIER_VISA_MUTATION from '@/graphql/mutations/dossierVisa.gql'
import WILAYAS_QUERY from '@/graphql/queries/wilayasQuery.gql'

export default {
  name: 'visas',

  components: {
    Status,
    Operator,
    EditContacts,
    EditTransactions,
    TreatmentSteps,
    PassportUpload,
    EditVisa
  },

  data () {
    return {
      searchQuery: '',
      searchType: 'pnr',
      wilayas: [],
      done: true,

      statusDialog: { open: false, dossier: {} },
      operatorDialog: { open: false, dossier: {} },
      contactsDialog: { open: false, dossier: {} },
      transactionsDialog: { open: false, dossier: {} },
      treatmentDialog: { open: false, dossier: {} },
      passportUploadDialog: { open: false, dossier: {} },
      editVisaDialog: { open: false, dossier: {} },

      visas: [],
      status: 'NEW',
      pageNumber: 1,
      pageSize: 5
    }
  },

  computed: {
    statusVisas () { return this.$store.getters.statusVisas },

    page: {
      get () {
        return this.pageNumber
      },

      set (value) {
        const page = parseInt(value, 10)
        if (page <= 0) {
          this.pageNumber = 1
        } else {
          this.pageNumber = page
        }
      }
    },

    variables () {
      return {
        status: this.status,
        page: this.page,
        pageSize: this.pageSize
      }
    },

    visasFiltered () {
      if (this.visas.data) {
        return orderBy(this.visas.data.filter(el => {
          return el.status === this.status
        }), '_id')
      }

      return []
    }
  },

  apollo: {
    visas: {
      query: VISAS_QUERY,
      variables () {
        return { ...this.variables }
      },
      fetchPolicy: 'network-only'
    },

    wilayas: WILAYAS_QUERY
  },

  methods: {
    editStatus (dossier) { this.statusDialog = { dossier, open: true } },
    editOperator (dossier) { this.operatorDialog = { dossier, open: true } },
    editContacts (dossier) { this.contactsDialog = { dossier, open: true } },
    editTransactions (dossier) { this.transactionsDialog = { dossier, open: true } },
    openTreatmentDialog (dossier) { this.treatmentDialog = { dossier, open: true } },
    openPassportDialog (dossier) { this.passportUploadDialog = { dossier, open: true } },
    editVisa (dossier) { this.editVisaDialog = { dossier, open: true } },
    editName (dossier) {
      this.$dialog.prompt({
        title: 'Nom :',
        cancelText: 'Annuler',
        confirmText: 'Confirmer',
        inputAttrs: {
          value: dossier.name
        },
        onConfirm: (value) => {
          this.updateDossier({
            _id: dossier._id,
            name: value
          })
        }
      })
    },

    passportError () {
      this.$dialog.alert({
        type: 'is-danger',
        hasIcon: true,
        title: 'Passport requis',
        message: 'Vous devez uploader le passport du client avant de pourvoir changer de status'
      })
    },

    visaError () {
      this.$dialog.alert({
        type: 'is-danger',
        hasIcon: true,
        title: 'Visa requis',
        message: 'Vous devez uploader le visa du client avant de pourvoir changer de status'
      })
    },

    changeStatus (status) {
      if (!this.$apollo.queries.visas.loading) {
        this.status = status
      }
    },

    assignDossier (dossier) {
      if (dossier.operator) {
        if (this.$user._id === dossier.operator._id) return
      }

      this.$deleteModal.prompt({
        title: 'Assigner dossier',
        text: `Êtes-vous sûr de vouloir vous assigner ce dossier ?`,
        onConfirm: () => {
          this.updateDossier({
            _id: dossier._id,
            operator: this.$user._id
          })
        }
      })
    },

    async updateSteps (params) {
      await this.updateDossier(params)
      const dossier = find(this.visas.data, { _id: params._id })
      this.treatmentDialog.dossier = dossier
    },

    async updatePassport (params) {
      await this.updateDossier(params)
      const dossier = find(this.visas.data, { _id: params._id })
      this.passportUploadDialog.dossier = dossier
    },

    async updateVisa (params) {
      await this.updateDossier(params)
      const dossier = find(this.visas.data, { _id: params._id })
      this.treatmentDialog.dossier = dossier
    },

    async updateDossier (params) {
      this.done = false

      try {
        await this.$apollo.mutate({
          mutation: DOSSIER_VISA_MUTATION,
          variables: params,
          update: (store, { data: { editDossierVisa } }) => {
            let data = store.readQuery({
              query: VISAS_QUERY,
              variables: { ...this.variables }
            })
            const key = findKey(data.visas.data, { _id: params._id })

            data.visas.data[key] = {
              ...data.visas.data[key],
              ...editDossierVisa
            }

            store.writeQuery({
              query: VISAS_QUERY,
              variables: { ...this.variables },
              data
            })
          }
        })
        this.statusDialog.open = false
        this.done = true
      } catch (error) {
        console.error(error)
        this.statusDialog.open = false
        this.done = true
      }
    }
  }
}
</script>



