<template>
  <div class="content-wrapper">
    <!-- New price button -->
    <div style="text-align: right">
      <mu-flat-button label="Ajouter prix" @click.prevent="addPrice()" v-if="$can('create', 'prix-visas')" />
    </div>

    <b-table :data="visasPrices" striped :loading="$apollo.queries.visasPrices.loading">
      <template slot-scope="props">
        <b-table-column field="code" label="Code">
          {{ props.row.code }}
        </b-table-column>

        <b-table-column field="displayName" label="Nom">
          {{ props.row.displayName }}
        </b-table-column>

        <b-table-column field="price" label="Prix">
          {{ props.row.price }}
        </b-table-column>

        <b-table-column field="commission" label="Commission">
          {{ props.row.commission }}
        </b-table-column>

        <b-table-column label="Options">
          <!-- Edit button -->
          <b-tooltip label="Editer" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="editPrice(props.row)"
              v-if="$can('update', 'prix-visas')"
            >
              <mu-icon value="edit" :size="18"/>
            </a>
          </b-tooltip>

          <!-- Delete button -->
          <b-tooltip label="Supprimer" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="promptRemovePrice(props.row)"
              v-if="$can('delete', 'prix-visas')"
            >
              <mu-icon value="delete" :size="18"/>
            </a>
          </b-tooltip>
        </b-table-column>
      </template>
    </b-table>

    <edit-visa-price
      :open.sync="modal"
      :visaPrice="visaPrice"
      :deposits="visasDeposits"
      @update="save" />
  </div>
</template>

<script>
import { findKey } from 'lodash'
import VISAS_PRICES_QUERY from '@/graphql/queries/visasPricesQuery.gql'
import ADD_VISA_PRICE_MUTATION from '@/graphql/mutations/addVisaPrice.gql'
import EDIT_VISA_PRICE_MUTATION from '@/graphql/mutations/editVisaPrice.gql'
import DELETE_VISA_PRICE_MUTATION from '@/graphql/mutations/deleteVisaPrice.gql'
import VISAS_DEPOSITS_QUERY from '@/graphql/queries/visasDepositsQuery.gql'
import EditVisaPrice from '@/components/visas-prices/EditVisaPrice'

export default {
  name: 'visas-prices',
  components: { EditVisaPrice },

  data () {
    return {
      visasPrices: [],
      visaPrice: {},
      modal: false
    }
  },

  apollo: {
    visasPrices: {
      query: VISAS_PRICES_QUERY,
      fetchPolicy: 'network-only'
    },

    visasDeposits: {
      query: VISAS_DEPOSITS_QUERY,
      fetchPolicy: 'network-only'
    }
  },

  methods: {
    addPrice () { this.editPrice({}) },

    editPrice (price) {
      this.visaPrice = price
      this.modal = true
    },

    save (price) {
      if (!price._id) {
        this.addPriceRequest(price)
      } else {
        this.editPriceRequest(price)
      }
    },

    promptRemovePrice (price) {
      this.$deleteModal.prompt({
        title: 'Supprimer prix',
        onConfirm: () => {
          this.removePrice(price._id)
        }
      })
    },

    removePrice (_id) {
      try {
        this.$apollo.mutate({
          mutation: DELETE_VISA_PRICE_MUTATION,
          variables: { _id },
          update (store, { response }) {
            let data = store.readQuery({ query: VISAS_PRICES_QUERY })
            // Get price index
            const indexPrice = findKey(data.visasPrices, { _id })

            data.visasPrices.splice(indexPrice, 1)

            store.writeQuery({
              query: VISAS_PRICES_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    },

    async addPriceRequest (price) {
      try {
        await this.$apollo.mutate({
          mutation: ADD_VISA_PRICE_MUTATION,
          variables: price,
          update (store, { data: { addVisaPrice } }) {
            let data = store.readQuery({ query: VISAS_PRICES_QUERY })
            data.visasPrices.push(addVisaPrice)

            store.writeQuery({
              query: VISAS_PRICES_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    },

    async editPriceRequest (price) {
      try {
        await this.$apollo.mutate({
          mutation: EDIT_VISA_PRICE_MUTATION,
          variables: price,
          update (store, { data: { editVisaPrice } }) {
            let data = store.readQuery({ query: VISAS_PRICES_QUERY })
            const indexPrice = findKey(data.visasPrices, { _id: price._id })

            data.visasPrices[indexPrice] = editVisaPrice

            store.writeQuery({
              query: VISAS_PRICES_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>



