<template>
  <mu-dialog :open="open" @close="close" title="Utilisateur">
    <div>
      <!-- Username -->
      <mu-text-field
        v-model="user.username"
        label="Username"
        labelFloat
      />

      <mu-text-field
        v-model="user.password"
        type="password"
        label="Mot de passe"
        labelFloat
      />

      <br/>

      <!-- Nom -->
      <mu-text-field
        v-model="user.name"
        label="Nom"
        labelFloat
      />

      <!-- Email -->
      <mu-text-field
        v-model="user.email"
        label="Email"
        labelFloat
      />

      <br/>

      <!-- Roles -->
      <mu-select-field
        v-model="user.roles"
        multiple
        fullWidth
        label="Roles"
        labelFloat
        v-if="!$apollo.queries.roles.loading"
        separator="; "
      >
        <mu-menu-item
          v-for="(role, index) in roles" :key="index"
          :value="role.name" :title="role.name"
        />
      </mu-select-field>

      <!-- Wilayas -->
      <mu-select-field
        v-model="user.wilayas"
        label="Wilayas"
        labelFloat
        multiple
        :maxHeight="250"
        separator="; "
      >
        <mu-menu-item
          v-for="(w, index) in wilayasOrdered" :key="index"
          :value="parseInt(w._id, 10)" :title="`${w.code} - ${w.name}`"
        />
      </mu-select-field>

      <!-- Clear wilayas -->
      <b-tooltip label="Vider wilayas" position="is-top" style="padding: 0 1rem 0 .3rem">
        <a
          class="button is-small is-rounded"
          style="width: 36px; height: 36px"
          @click.prevent="user.wilayas = []"
        >
          <mu-icon value="delete" :size="18"/>
        </a>
      </b-tooltip>

      <!-- Communes -->
      <mu-select-field
        v-model="user.communes"
        multiple
        label="Communes"
        labelFloat
        separator="; "
        :maxHeight="250"
      >
        <mu-menu-item
          v-for="(commune, index) in communes" :key="index"
          :value="parseInt(commune._id, 10)" :title="commune.name"
        />
      </mu-select-field>

      <!-- Clear communes -->
      <b-tooltip label="Vider communes" position="is-top" style="padding: 0 1rem 0 .3rem">
        <a
          class="button is-small is-rounded"
          style="width: 36px; height: 36px"
          @click.prevent="user.communes = []"
        >
          <mu-icon value="delete" :size="18"/>
        </a>
      </b-tooltip>
    </div>

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
        />

        <!-- Button sauvegarder -->
        <mu-raised-button
          label="Sauvegarder"
          @click.prevent="save"
          style="margin: 0 1rem"
          primary
        />
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
import { cloneDeep, findKey, flatten, isEmpty, orderBy, find } from 'lodash'
import USERS_QUERY from '@/graphql/queries/usersQuery.gql'
import CREATE_USER_MUTATION from '@/graphql/mutations/createUser.gql'
import EDIT_USER_MUTATION from '@/graphql/mutations/editUser.gql'
import ROLES_QUERY from '@/graphql/queries/rolesQuery.gql'

export default {
  name: 'edit-user',
  props: ['open', 'data', 'wilayas'],

  data () {
    return {
      user: {},
      roles: []
    }
  },

  computed: {
    wilayasOrdered () {
      return orderBy(this.wilayas, 'code')
    },

    communes () {
      if (!isEmpty(this.user)) {
        const wilayas = this.user.wilayas
        let communes = []
        wilayas.forEach(id => { communes.push(find(this.wilayas, { code: id }).communes) })

        return orderBy(flatten(communes), 'name')
      }
    }
  },

  watch: {
    open (value) {
      if (value) {
        this.user = cloneDeep(this.data)
      }
    }
  },

  apollo: {
    roles: {
      query: ROLES_QUERY,
      fetchPolicy: 'network-only',
      skip () {
        return !this.open
      }
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    async save () {
      if (this.user._id) {
        await this.editUser()
      } else {
        await this.addUser()
      }

      this.close()
    },

    async addUser () {
      try {
        await this.$apollo.mutate({
          mutation: CREATE_USER_MUTATION,
          variables: {
            ...this.user
          },
          update (store, { data: { createUser } }) {
            let data = store.readQuery({ query: USERS_QUERY })
            data.users.push(createUser)

            store.writeQuery({
              query: USERS_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    },

    async editUser () {
      try {
        await this.$apollo.mutate({
          mutation: EDIT_USER_MUTATION,
          variables: {
            ...this.user
          },
          update: (store, { data: { updateUser } }) => {
            let data = store.readQuery({ query: USERS_QUERY })
            // Get user index
            const indexUser = findKey(data.users, {
              _id: this.user._id
            })

            data.users[indexUser] = updateUser

            store.writeQuery({
              query: USERS_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>



