<template>
  <mu-dialog :open="open" @close="close" title="Nouvelle commune">
    <mu-select-field
      v-model="wilaya"
      label="Wilaya"
      labelFloat
      style="vertical-align: bottom"
      fullWidth
    >
      <mu-menu-item
        v-for="(w, index) in wilayas" :key="index"
        :value="w._id" :title="w.name"
      />
    </mu-select-field>

    <mu-text-field
      v-model="commune"
      label="Nouvelle commune"
      labelFloat
    />

    <mu-text-field
      type="number"
      v-model="postalCode"
      label="Code postale"
      labelFloat
    />

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
          :disabled="!wilaya"
        />

        <!-- Button sauvegarder -->
        <mu-raised-button
          label="Sauvegarder"
          @click.prevent="save"
          style="margin: 0 1rem"
          primary
        />
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
import { findKey } from 'lodash'
import WILAYAS_QUERY from '@/graphql/queries/wilayasQuery.gql'
import ADD_COMMUNE_MUTATION from '@/graphql/mutations/addCommune.gql'

export default {
  name: 'add-commune',
  props: ['open', 'wilayas'],

  data () {
    return {
      wilaya: 0,
      commune: '',
      postalCode: ''
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    save () {
      this.$apollo.mutate({
        mutation: ADD_COMMUNE_MUTATION,
        variables: {
          wilaya: this.wilaya,
          commune: this.commune,
          postalCode: this.postalCode
        },
        update: (store, { data: { addCommune } }) => {
          let data = store.readQuery({ query: WILAYAS_QUERY })
          // Get wilaya index
          const indexWilaya = findKey(data.wilayas, {
            _id: this.wilaya
          })

          data.wilayas[indexWilaya].communes.push(addCommune)

          store.writeQuery({
            query: WILAYAS_QUERY,
            data
          })
        }
      })

      this.close()
    }
  }
}
</script>



