<template>
  <mu-dialog
    :title="`Dossier: H-${dossier._id} - ${dossier.bookingRef || dossier.secondaryBookingRef}`"
    :open="open"
    @close="close"
  >
    <div style="padding: 1rem 0">
      <p>Status: <b>{{ $get(dossier, 'statusBooster.status', '-') }}</b></p>

      <div class="buttons-box">
        <mu-raised-button :disabled="disabled" primary label="Confirmer" @click="confirmBooking" />
        <mu-raised-button :disabled="disabled" label="Check" @click="checkBooking" />
        <mu-raised-button :disabled="disabled" secondary label="Annuler" @click="cancelBooking" />
      </div>
    </div>

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
        />
      </div>
    </footer>

    <mu-dialog :open="dialog" title="Status" @close="closeDialog">
      <ul style="padding: 1rem 0">
        <li v-for="(info, key) in dialogInfo" :key="key">{{ key }}: {{ info }}</li>
      </ul>
    </mu-dialog>
  </mu-dialog>
</template>

<script>
import CONFIRM_HOTEL_RESERVATION from '@/graphql/mutations/confirmHotelReservation.gql'
import CANCEL_HOTEL_RESERVATION from '@/graphql/mutations/cancelHotelReservation.gql'
import CHECK_HOTEL_RESERVATION from '@/graphql/mutations/checkHotelReservation.gql'

export default {
  name: 'edit-hotel-dialog',
  props: ['open', 'dossier', 'variables'],

  data () {
    return {
      dialog: false,
      dialogInfo: '',
      disabled: false
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    closeDialog () {
      this.dialog = false
    },

    async confirmBooking () {
      const bookingId = this.dossier.bookingId

      try {
        this.disabled = true

        await this.$apollo.mutate({
          mutation: CONFIRM_HOTEL_RESERVATION,
          variables: { bookingId },
          update: (store, { data: { confirmHotelReservation } }) => {
            this.showHbResponse(confirmHotelReservation)
            this.disabled = false
          }
        })
      } catch (error) {
        this.disabled = false
        this.showHbResponse(error)
        console.error(error)
      }
    },

    async cancelBooking () {
      const bookingId = this.dossier.bookingId

      try {
        this.disabled = true

        await this.$apollo.mutate({
          mutation: CANCEL_HOTEL_RESERVATION,
          variables: { bookingId },
          update: (store, { data: { cancelHotelReservation } }) => {
            this.showHbResponse(cancelHotelReservation)
            this.disabled = false
          }
        })
      } catch (error) {
        this.disabled = false
        this.showHbResponse(error)
        console.error(error)
      }
    },

    async checkBooking () {
      const bookingId = this.dossier.bookingId

      try {
        this.disabled = true

        await this.$apollo.mutate({
          mutation: CHECK_HOTEL_RESERVATION,
          variables: { bookingId },
          update: (store, { data: { checkHotelReservation } }) => {
            this.showHbResponse(checkHotelReservation)
            this.disabled = false
          }
        })
      } catch (error) {
        this.disabled = false
        this.showHbResponse(error)
        console.error(error)
      }
    },

    showHbResponse (response) {
      this.dialog = true
      this.dialogInfo = response
    }
  }
}
</script>

<style lang="scss" scoped>
.buttons-box {
  padding-top: 1rem;
}
</style>



