<template>
  <div class="content-wrapper">
    <!-- New deposit button -->
    <div style="text-align: right">
      <mu-flat-button label="Ajouter Deposit" @click.prevent="addDeposit()" v-if="$can('create', 'prix-visas')" />
    </div>

    <b-table :data="visasDeposits" striped :loading="$apollo.queries.visasDeposits.loading">
      <template slot-scope="props">
        <b-table-column field="name" label="Nom">
          {{ props.row.name }}
        </b-table-column>

        <b-table-column field="balance" label="Balance">
          {{ formatMoney(props.row.balance) }}
        </b-table-column>

        <b-table-column label="Actions">
          <!-- Increment deposit -->
          <b-tooltip
            label="Versement"
            position="is-top"
            v-if="!$can('operateurs', 'update')"
          >
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="incrementDeposit(props.row)"
            >
              <mu-icon value="add" :size="24" />
            </a>
          </b-tooltip>

          <!-- Deposit history -->
          <b-tooltip
            label="Historique"
            position="is-top"
            v-if="!$can('operateurs', 'update')"
          >
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="showHistory(props.row)"
            >
              <mu-icon value="history" :size="24" />
            </a>
          </b-tooltip>
        </b-table-column>
      </template>
    </b-table>

    <add-visa-deposit :open.sync="addDepositModal" :visaDeposit="deposit" @update="save" />
    <increment-deposit :open.sync="incrementModal" :visaDeposit="deposit" @update="incrementDepositRequest" :depositId="incrementDepositId"/>
    <deposit-history :open.sync="historyModal" :history="depositHistory" />
  </div>
</template>

<script>
import { isEmpty } from 'lodash'

import VISAS_DEPOSITS_QUERY from '@/graphql/queries/visasDepositsQuery.gql'
import ADD_VISA_DEPOSIT_MUTATION from '@/graphql/mutations/addVisaDeposit.gql'
import INCREMENT_VISA_DEPOSIT from '@/graphql/mutations/incrementVisaDeposit.gql'
import AddVisaDeposit from '@/components/visas-deposits/AddVisaDeposit'
import IncrementDeposit from '@/components/visas-deposits/IncrementDeposit'
import DepositHistory from '@/components/visas-deposits/DepositHistory'

export default {
  name: 'deposits',
  components: { AddVisaDeposit, IncrementDeposit, DepositHistory },

  data () {
    return {
      addDepositModal: false,
      incrementModal: false,
      historyModal: false,
      deposit: {},
      incrementDepositId: null,
      depositHistory: false
    }
  },

  apollo: {
    visasDeposits: {
      query: VISAS_DEPOSITS_QUERY,
      fetchPolicy: 'network-only'
    }
  },

  methods: {
    addDeposit () { this.editDeposit({}) },

    editDeposit (deposit) {
      this.visaDeposit = deposit
      this.addDepositModal = true
    },

    incrementDeposit (deposit) {
      this.incrementDepositId = deposit._id
      this.incrementModal = true
    },

    incrementDepositRequest (deposit) {
      try {
        this.$apollo.mutate({
          mutation: INCREMENT_VISA_DEPOSIT,
          variables: deposit,
          update (store, { data: { incrementVisaDeposit } }) {
            let data = store.readQuery({ query: VISAS_DEPOSITS_QUERY })
            const depositIndex = data.visasDeposits.findIndex(deposit => {
              return deposit._id === incrementVisaDeposit.depositId
            })

            if (isEmpty(data.visasDeposits[depositIndex].history)) {
              data.visasDeposits[depositIndex].history = [incrementVisaDeposit]
            } else {
              data.visasDeposits[depositIndex].history.push(incrementVisaDeposit)
            }
            data.visasDeposits[depositIndex].balance += incrementVisaDeposit.amount

            store.writeQuery({
              query: VISAS_DEPOSITS_QUERY,
              data
            })
          }
        })
      } catch (e) {
        console.error(e)
      }
    },

    showHistory (deposit) {
      this.depositHistory = deposit.history
      this.historyModal = true
    },

    save (deposit) {
      if (!deposit._id) {
        this.addDepositRequest(deposit)
      } else {
        this.editDepositRequest(deposit)
      }
    },

    async addDepositRequest (deposit) {
      try {
        await this.$apollo.mutate({
          mutation: ADD_VISA_DEPOSIT_MUTATION,
          variables: deposit,
          update (store, { data: { addVisaDeposit } }) {
            let data = store.readQuery({ query: VISAS_DEPOSITS_QUERY })
            data.visasDeposits.push(addVisaDeposit)

            store.writeQuery({
              query: VISAS_DEPOSITS_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>



