<template>
  <div class="content-wrapper">
    <!-- New commune button -->
    <div style="text-align: right">
      <mu-flat-button label="Ajouter commune" @click.prevent="newCommune()" />
    </div>

    <b-table :data="wilayas" striped :loading="$apollo.queries.wilayas.loading">
      <template slot-scope="props">
        <!-- Wilaya -->
        <b-table-column label="Wilaya">
          {{ props.row.name }}
        </b-table-column>

        <!-- Communes -->
        <b-table-column label="Communes">
          <span
            class="communes"
            v-for="(commune, index) in props.row.communes"
            :key="index"
          >{{ commune.name }}</span>
        </b-table-column>
      </template>
    </b-table>

    <add-commune :open.sync="communeModal" :wilayas="wilayas"/>
  </div>
</template>

<script>
import WILAYAS_QUERY from '@/graphql/queries/wilayasQuery.gql'
import AddCommune from '@/components/wilayas/AddCommune'

export default {
  name: 'wilayas',
  components: { AddCommune },

  data () {
    return {
      wilayas: [],
      communeModal: false
    }
  },

  apollo: {
    wilayas: {
      query: WILAYAS_QUERY,
      fetchPolicy: 'network-only'
    }
  },

  methods: {
    newCommune () {
      this.communeModal = true
    }
  }
}
</script>

<style lang="scss">
  .communes {
    &::after {
      content: ' - '
    }

    &:last-child::after {
      content: ''
    }
  }
</style>



