<template>
  <div class="content-wrapper">
    <!-- New user button -->
    <div style="text-align: right">
      <mu-flat-button label="Ajouter utilisateur" @click.prevent="newUser()" />
    </div>

    <b-table :data="users" striped :loading="$apollo.queries.users.loading">
      <template slot-scope="props">
        <!-- Username -->
        <b-table-column field="username" label="Username">
          <a @click.prevent="editUser(props.row)">{{ props.row.username }}</a>
        </b-table-column>

        <!-- Name -->
        <b-table-column field="name" label="Nom">
          {{ props.row.name }}
        </b-table-column>

        <!-- Email -->
        <b-table-column field="email" label="Email">
          {{ props.row.email }}
        </b-table-column>

        <!-- Roles -->
        <b-table-column field="roles" label="Roles">
          <span class="role-span" v-for="(role, index) in props.row.roles" :key="index">{{role}}</span>
        </b-table-column>

        <!-- Options -->
        <b-table-column label="Options">
          <!-- Edit -->
          <b-tooltip label="Editer" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="editUser(props.row)"
            >
              <mu-icon value="edit" :size="18"/>
            </a>
          </b-tooltip>
          <b-tooltip label="Supprimer" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="promptRemoveUser(props.row)"
            >
              <mu-icon value="delete" :size="18"/>
            </a>
          </b-tooltip>
        </b-table-column>
      </template>
    </b-table>

    <edit-user :open.sync="userModal" :data="user" :wilayas="wilayas" />
  </div>
</template>

<script>
import { findKey } from 'lodash'
import EditUser from '@/components/edit-user'
import wilayasQuery from '@/graphql/queries/wilayasQuery.gql'
import USERS_QUERY from '@/graphql/queries/usersQuery.gql'
import REMOVE_USER_MUTATION from '@/graphql/mutations/deleteUser.gql'

export default {
  name: 'users',
  components: { EditUser },

  data () {
    return {
      users: [],
      user: {},
      userModal: false,
      wilayas: []
    }
  },

  apollo: {
    users: {
      query: USERS_QUERY,
      fetchPolicy: 'network-only'
    },

    wilayas: {
      query: wilayasQuery,
      fetchPolicy: 'network-only'
    }
  },

  methods: {
    editUser (user) {
      this.user = user
      this.userModal = true
    },

    newUser () {
      this.editUser({ permissions: [], roles: [], wilayas: [], communes: [] })
    },

    promptRemoveUser (user) {
      this.$deleteModal.prompt({
        title: 'Supprimer utilisateur',
        onConfirm: () => {
          this.removeUser(user._id)
        }
      })
    },

    async removeUser (_id) {
      try {
        await this.$apollo.mutate({
          mutation: REMOVE_USER_MUTATION,
          variables: { _id },
          update (store, { response }) {
            let data = store.readQuery({ query: USERS_QUERY })
            // Get user index
            const indexUser = findKey(data.users, { _id })

            data.users.splice(indexUser, 1)

            store.writeQuery({
              query: USERS_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>

<style lang="scss">
  .role-span {
    &::after {
      content: ' - '
    }

    &:last-child::after {
      content: ''
    }
  }
</style>



