<template>
  <mu-dialog
    :title="`Dossier: #${dossier._id} - ${dossier.bookingRef || dossier.secondaryBookingRef}`"
    :open="open"
    @close="close"
  >
    <!-- Body content -->
    <div style="padding-top: 1rem" v-if="Object.keys(this.dossier).length">
      <!-- Réservations -->
      <mu-paper class="paper-card">
        <div class="title-bar">
          <h2>
            Réservations
            <mu-badge :content="String(dossier.paxes.length)"></mu-badge>
          </h2>
        </div>

        <b-table :data="dossier.paxes" striped narrowed>
          <template slot-scope="props">
            <!-- Titre invité -->
            <b-table-column label="Titre">
              {{ props.row.title || '-' }}
            </b-table-column>

            <!-- Nom invité -->
            <b-table-column label="Nom">
              {{ props.row.lastName }}
            </b-table-column>

            <!-- Prenom invité -->
            <b-table-column label="Prenom">
              {{ props.row.firstName }}
            </b-table-column>

            <!-- Age invité -->
            <b-table-column label="Âge">
              {{ props.row.age || '-' }}
            </b-table-column>

            <!-- Type invité -->
            <b-table-column label="Type">
              {{ props.row.paxType || '-' }}
            </b-table-column>
          </template>
        </b-table>
      </mu-paper>

      <!-- Chambres -->
      <mu-paper class="paper-card">
        <div class="title-bar">
          <h2>
            Chambres
            <mu-badge :content="String(dossier.rooms.length)"></mu-badge>
          </h2>
        </div>

        <b-table :data="dossier.rooms" striped narrowed>
          <template slot-scope="props">
            <!-- Chambre -->
            <b-table-column field="room" label="Chambre">
              {{ props.row.room }}
            </b-table-column>

            <!-- Accommodations -->
            <b-table-column field="board" label="Accommodations">
              {{ props.row.board }}
            </b-table-column>

            <!-- Quantité -->
            <b-table-column field="quantity" label="Quantité">
              {{ props.row.quantity }}
            </b-table-column>

            <!-- Invités -->
            <b-table-column label="Invités">
              Adultes: {{ props.row.paxes[0].adults }}<br/>
              Enfants: {{ props.row.paxes[0].infant }}<br/>
              Nourrissons: {{ props.row.paxes[0].child }}
            </b-table-column>
          </template>
        </b-table>
      </mu-paper>

      <mu-paper class="paper-card">
        <div class="title-bar"><h2>Informations supplémentaires</h2></div>

        <table class="table is-fullwidth">
          <tr>
            <td><b>Préférences client:</b></td>
            <td>{{ dossier.preferences }}</td>
          </tr>

          <tr>
            <td><b>Commentaires platforme:</b></td>
            <td>{{ dossier.comments }}</td>
          </tr>

          <tr>
            <td><b>Détails de paiements:</b></td>
            <td v-html="dossier.paymenDetails"></td>
          </tr>
        </table>
      </mu-paper>
    </div>
  </mu-dialog>
</template>

<script>
export default {
  name: 'dossier-hotel',
  props: ['open', 'dossier'],

  methods: {
    close () {
      this.$emit('update:open', false)
    }
  }
}
</script>



