<template>
  <mu-dialog :open="open" @close="close" title="Status de traitement">
    <div class="steps-container" v-if="Object.keys(dossier).length">
      <mu-stepper :active-step="activeStep" orientation="vertical">
        <mu-step>
          <mu-step-label>Étape 01</mu-step-label>
          <mu-step-content>
            <p class="step-text">Valider l'envoie de la demande</p>
            <mu-raised-button @click="firstStep" primary>Valider</mu-raised-button>
          </mu-step-content>
        </mu-step>

        <mu-step>
          <mu-step-label>Étape 02</mu-step-label>
          <mu-step-content>
            <p class="step-text">
              Accuser la réception de la requête par le partenaire
            </p>
            <mu-raised-button @click="secondStep" primary>Valider</mu-raised-button>
          </mu-step-content>
        </mu-step>

        <mu-step>
          <mu-step-label>Étape 03</mu-step-label>
          <mu-step-content>
            <p class="step-text">
              Accuser la réception de la réponse du partenaire
            </p>
            <mu-raised-button @click="thirdStep" primary>Valider</mu-raised-button>
          </mu-step-content>
        </mu-step>

        <mu-step>
          <mu-step-label>Étape 04</mu-step-label>
          <mu-step-content>
            <p class="step-text">
              <mu-icon value="check" style="vertical-align: bottom" /> Validé
            </p>

            <!-- Download visa button -->
            <mu-raised-button
              style="vertical-align: middle"
              download
              icon="arrow_downward"
              secondary
              :disabled="!dossier.visa"
              :href="`/api/static${dossier.visa}`"
            >Télécharger visa&nbsp;</mu-raised-button>

            <!-- Upload visa button -->
            <mu-raised-button
              style="vertical-align: middle"
              icon="arrow_upward"
              primary
              @click="visaUploadDialog = true"
            >Upload visa&nbsp;</mu-raised-button>
          </mu-step-content>
        </mu-step>
      </mu-stepper>
    </div>

    <footer>
      <div style="padding: 1rem; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          secondary label="Fermer"
          @click.prevent="close"
        ></mu-raised-button>
      </div>
    </footer>

    <!-- Visa upload -->
    <visa-upload
      :open.sync="visaUploadDialog"
      :dossier="dossier"
      @update="updateVisa"
      :done="done"
    />
  </mu-dialog>
</template>

<script>
import VisaUpload from '@/components/visas/VisaUpload'

export default {
  name: 'visa-treatment-steps',
  props: ['open', 'dossier', 'done'],
  components: { VisaUpload },

  data () {
    return {
      visaUploadDialog: false
    }
  },

  computed: {
    activeStep () {
      if (!this.dossier.treatmentStatus || !this.dossier.treatmentStatus.sent) {
        return 0
      }

      if (!this.dossier.treatmentStatus.received) {
        return 1
      }

      if (!this.dossier.treatmentStatus.response) {
        return 2
      }

      return 3
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    update (params) {
      this.$emit('update', params)
    },

    openVisaDialog (dossier) { this.visaUploadDialog = { dossier, open: true } },

    updateVisa (params) {
      this.$emit('update-visa', params)
    },

    firstStep () {
      const request = {
        _id: this.dossier._id,
        treatmentStatus: {
          ...this.dossier.treatmentStatus,
          sent: true
        }
      }

      this.$emit('update', request)
    },

    secondStep () {
      const request = {
        _id: this.dossier._id,
        treatmentStatus: {
          ...this.dossier.treatmentStatus,
          received: true
        }
      }

      this.$emit('update', request)
    },

    thirdStep () {
      const request = {
        _id: this.dossier._id,
        treatmentStatus: {
          ...this.dossier.treatmentStatus,
          response: true
        }
      }

      this.$emit('update', request)
    }
  }
}
</script>

<style lang="scss" scoped>
.steps-container {
  min-width: 450px;
  padding: 1rem;

  .step-text {
    padding: 1rem 0;
  }
}
</style>



