<template>
  <div class="content-wrapper">
    <!-- New user button -->
    <div style="text-align: right">
      <mu-flat-button label="Ajouter zone" @click.prevent="newDeliveryZone()" />
    </div>

    <b-table :data="deliveryZones" striped :loading="$apollo.queries.deliveryZones.loading">
      <template slot-scope="props">
        <!-- Wilaya -->
        <b-table-column label="Wilaya">
          {{ props.row.wilaya.name }}
        </b-table-column>

        <!-- Communes -->
        <b-table-column label="Communes">
          <span
            class="communes"
            v-for="(commune, index) in props.row.communes"
            :key="index"
          >{{ commune.name }}</span>
        </b-table-column>

        <!-- Options -->
        <b-table-column label="Options">
          <!-- Edit -->
          <b-tooltip label="Editer" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="editZone(props.row)"
            >
              <mu-icon value="edit" :size="18"/>
            </a>
          </b-tooltip>
          <b-tooltip label="Supprimer" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="promptRemoveZone(props.row)"
            >
              <mu-icon value="delete" :size="18"/>
            </a>
          </b-tooltip>
        </b-table-column>
      </template>
    </b-table>

    <edit-delivery-zone :open.sync="zoneModal" :zone="zone" :wilayas="wilayas" :newZone="newZone" />
  </div>
</template>

<script>
import { findKey } from 'lodash'
import DELIVERY_ZONES_QUERY from '@/graphql/queries/deliveryZonesQuery.gql'
import DELETE_DELIVERY_ZONE from '@/graphql/mutations/deleteDeliveryZone.gql'
import WILAYAS_QUERY from '@/graphql/queries/wilayasQuery.gql'
import EditDeliveryZone from '@/components/delivery-zones/EditDeliveryZone'

export default {
  name: 'delivery-zones',
  components: { EditDeliveryZone },

  data () {
    return {
      deliveryZones: [],
      wilayas: [],
      zone: {},
      zoneModal: false,
      newZone: false
    }
  },

  apollo: {
    deliveryZones: {
      query: DELIVERY_ZONES_QUERY,
      fetchPolicy: 'network-only'
    },

    wilayas: {
      query: WILAYAS_QUERY,
      fetchPolicy: 'network-only'
    }
  },

  methods: {
    newDeliveryZone () {
      this.newZone = true
      this.zoneModal = true
    },

    editZone (zone) {
      this.newZone = false
      this.zone = zone
      this.zoneModal = true
    },

    promptRemoveZone (zone) {
      this.$deleteModal.prompt({
        title: 'Supprimer zone livraison',
        onConfirm: () => {
          this.deleteZone(zone._id)
        }
      })
    },

    async deleteZone (_id) {
      try {
        await this.$apollo.mutate({
          mutation: DELETE_DELIVERY_ZONE,
          variables: { _id },
          update (store, { response }) {
            let data = store.readQuery({ query: DELIVERY_ZONES_QUERY })
            // Get user index
            const indexZone = findKey(data.deliveryZones, { _id })

            data.deliveryZones.splice(indexZone, 1)

            store.writeQuery({
              query: DELIVERY_ZONES_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>

<style lang="scss">
  .communes {
    &::after {
      content: ' - '
    }

    &:last-child::after {
      content: ''
    }
  }
</style>



