<template>
  <mu-dialog :open="open" @close="close" title="Status">
    <div style="min-width: 300px">
      <mu-select-field v-model="status" label="Status" labelFloat fullWidth>
        <template v-if="type === 'vol' || type === 'hotel'">
          <mu-menu-item
            v-for="(label, index) in statusDossier"
            :key="index"
            :value="index"
            :title="label"
          />
        </template>

        <template v-if="type === 'visa'">
          <mu-menu-item
            v-for="(label, index) in statusVisas"
            :key="index"
            :value="index"
            :title="label"
          />
        </template>

        <template v-if="type === 'telex'">
          <mu-menu-item
            v-for="(label, index) in statusTelex"
            :key="index"
            :value="index"
            :title="label"
          />
        </template>

        <template v-if="type === 'assurance'">
          <mu-menu-item
            v-for="(label, index) in statusAssurances"
            :key="index"
            :value="index"
            :title="label"
          />
        </template>
      </mu-select-field>

      <br/>

      <!-- Footer -->
      <footer>
        <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
          <!-- Button annuler -->
          <mu-raised-button
            secondary label="Fermer"
            @click.prevent="close"
          ></mu-raised-button>

          <!-- Button valider -->
          <mu-raised-button
            primary
            label="Valider"
            style="margin: 0 .5rem"
            @click.prevent="update"
            :loading="loading"
          ></mu-raised-button>
        </div>
      </footer>
    </div>
  </mu-dialog>
</template>

<script>
export default {
  name: 'status',
  props: ['open', 'dossier', 'type'],

  data () {
    return {
      status: null,
      loading: false
    }
  },

  watch: {
    dossier (value) {
      this.status = value.status
    }
  },

  computed: {
    statusDossier () { return this.$store.getters.status },
    statusVisas () { return this.$store.getters.statusVisas },
    statusTelex () { return this.$store.getters.statusTelex },
    statusAssurances () { return this.$store.getters.statusAssurances }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    update () {
      this.loading = true
      const statusVisaBeforeVendu = ['NEW', 'REL', 'ANN', 'VEN']

      if (this.type === 'vol') {
        this.$emit('update', {
          pnr: this.dossier.pnr.original,
          status: this.status
        })
        this.loading = false
        this.close()
      } else {
        if (this.type === 'visa') {
          if (!this.dossier.visa && this.status === 'DONE') {
            this.$emit('no-visa')
            return
          }

          if (!this.dossier.passport && !statusVisaBeforeVendu.includes(this.status)) {
            this.$emit('no-passport')
            return
          }
        }

        this.$emit('update', {
          _id: this.dossier._id,
          status: this.status
        })
        this.loading = false
        this.close()
      }
    }
  }
}
</script>



