import * as types from '@/store/mutation-types'

export default {
  [types.SET_PAGE_TITLE] (state, { title }) {
    state.pageTitle = title
  },

  [types.SET_LOGGED_IN] (state, { status }) {
    state.isLoggedIn = status
  },

  [types.SET_USER] (state, { user }) {
    state.user = user
  }
}



