<template>
  <mu-dialog :open="open" @close="close" title="Commission">
    <div>
      <mu-select-field
        v-model="comm.type"
        label="Type"
        labelFloat
        style="vertical-align: bottom"
      >
        <mu-menu-item value="vol" title="Vol" />
        <mu-menu-item value="hotel" title="Hotel" disabled />
      </mu-select-field>

      <mu-text-field
        label="Code"
        v-model="comm.code"
        labelFloat
      />

      <br/>

      <mu-text-field
        label="Commission %"
        type="number"
        v-model="comm.initial"
        lablFloat
      />

      <mu-text-field
        label="Ajouté"
        type="number"
        v-model="comm.added"
        lablFloat
      />
    </div>

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
        />

        <!-- Button sauvegarder -->
        <mu-raised-button
          label="Sauvegarder"
          @click.prevent="save"
          style="margin: 0 1rem"
          primary
        />
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
import { cloneDeep, findKey } from 'lodash'
import COMMISSIONS_QUERY from '@/graphql/queries/commissionsQuery.gql'
import ADD_COMMISSION_MUTATION from '@/graphql/mutations/addCommission.gql'

export default {
  name: 'edit-commission',
  props: ['open', 'commission'],

  data () {
    return {
      comm: {}
    }
  },

  watch: {
    commission (value) {
      this.comm = cloneDeep(value)
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    async save () {
      if (this.comm._id) {
        await this.editCommission()
      } else {
        await this.addCommission()
      }

      this.close()
    },

    async addCommission () {
      try {
        await this.$apollo.mutate({
          mutation: ADD_COMMISSION_MUTATION,
          variables: {
            ...this.comm
          },
          update (store, { data: { addCommission } }) {
            let data = store.readQuery({ query: COMMISSIONS_QUERY })
            data.commissions.push(addCommission)

            store.writeQuery({
              query: COMMISSIONS_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    },

    async editCommission () {
      try {
        await this.$apollo.mutate({
          mutation: ADD_COMMISSION_MUTATION,
          variables: {
            ...this.comm
          },
          update: (store, { data: { editCommission } }) => {
            let data = store.readQuery({ query: COMMISSIONS_QUERY })
            // Get dossier index
            const indexCommission = findKey(data.commissions, {
              _id: this.comm._id
            })

            data.commissions[indexCommission] = editCommission

            store.writeQuery({
              query: COMMISSIONS_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>



