<template>
  <mu-dialog :open="open" @close="close" title="Historique deposit">
    <b-table :data="reversedHistory" striped>
      <template slot-scope="props">
        <b-table-column field="amount" label="Versement/Paiement">
          <span :class="{ 'has-text-success' : props.row.amount > 0, 'has-text-danger': props.row.amount < 0 }">
            {{ props.row.amount > 0 ? '+' : '' }}{{ formatMoney(props.row.amount) }}
          </span>
        </b-table-column>

        <b-table-column field="comment" label="Commentaire">
          {{ props.row.comment || '-' }}
        </b-table-column>

        <b-table-column field="createdAt" label="Date">
          {{ formatDate(props.row.createdAt) }}
        </b-table-column>
      </template>
    </b-table>
  </mu-dialog>
</template>

<script>
import { flatten } from 'lodash'

export default {
  name: 'deposit-history',
  props: ['open', 'history'],

  computed: {
    reversedHistory () {
      if (this.history) {
        return flatten(this.history).reverse()
      }
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    }
  }
}
</script>



