import * as types from '@/store/mutation-types'

export const setPageTitle = ({ commit }, { title }) => {
  commit(types.SET_PAGE_TITLE, { title })
}

export const setLoggedIn = ({ commit }) => {
  commit(types.SET_LOGGED_IN, { status: true })
}

export const setLoggedOut = ({ commit }) => {
  commit(types.SET_USER, { user: {} })
  commit(types.SET_LOGGED_IN, { status: false })
}

export const setUser = ({ commit }, { user }) => {
  commit(types.SET_USER, { user })
}



