<template>
  <div class="content-wrapper">
    <!-- New destination button -->
    <div style="text-align: right">
      <mu-flat-button :disabled="disabled" label="Ajouter destination" @click.prevent="addDestination()" v-if="$can('create', 'destinations')" />
    </div>

    <!-- Destinations -->
    <b-table :data="destinations" striped :loading="$apollo.queries.destinations.loading">
      <template slot-scope="props">
        <b-table-column field="name" label="Nom" style="width: 80%">
          {{ props.row.displayName }}
        </b-table-column>

        <b-table-column label="Actions">
          <b-tooltip
            label="Editer"
            position="is-top"
            v-if="!$can('destinations', 'update')"
          >
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="editDestination(props.row)"
              :disabled="props.row.operator && $user._id === props.row.operator._id"
            >
              <mu-icon value="edit" :size="24" />
            </a>
          </b-tooltip>

          <b-tooltip
            label="Supprimer"
            position="is-top"
            v-if="!$can('destinations', 'update')"
          >
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="promptDeleteDestination(props.row)"
              :disabled="props.row.operator && $user._id === props.row.operator._id"
            >
              <mu-icon value="delete" :size="24" />
            </a>
          </b-tooltip>
        </b-table-column>
      </template>
    </b-table>

    <!-- Edit destination modal component -->
    <edit-destination :open.sync="edit" :data="destination" @update="save" />
  </div>
</template>

<script>
import { findKey } from 'lodash'
import DESTINATIONS_QUERY from '@/graphql/queries/destinationsQuery.gql'
import CREATE_DESTINATION_MUTATION from '@/graphql/mutations/createDestination.gql'
import UPDATE_DESTINATION_MUTATION from '@/graphql/mutations/updateDestination.gql'
import DELETE_DESTINATION_MUTATION from '@/graphql/mutations/deleteDestination.gql'
import EditDestination from '@/components/edit-destination'

export default {
  name: 'destinations',
  components: { EditDestination },

  data () {
    return {
      destinations: [],
      destination: {},
      edit: false,
      disabled: false
    }
  },

  apollo: {
    destinations: {
      query: DESTINATIONS_QUERY,
      fetchPolicy: 'network-only'
    }
  },

  methods: {
    addDestination () {
      this.editDestination({})
    },

    promptDeleteDestination (destination) {
      this.$deleteModal.prompt({
        title: 'Supprimer destination',
        text: `Êtes-vous sûr de vouloir supprimer cette destination`,
        onConfirm: () => { this.deleteDestination(destination) }
      })
    },

    editDestination (destination) {
      this.destination = destination
      this.edit = true
    },

    save (destination) {
      this.disabled = true

      if (!destination._id) {
        this.createDestination(destination)
      } else {
        this.updateDestination(destination)
      }
    },

    async createDestination (destination) {
      try {
        this.$apollo.mutate({
          mutation: CREATE_DESTINATION_MUTATION,
          variables: destination,
          update: (store, { data: { createDestination } }) => {
            this.disabled = false

            let data = store.readQuery({ query: DESTINATIONS_QUERY })

            data.destinations.push(createDestination)

            store.writeQuery({
              query: DESTINATIONS_QUERY,
              data
            })
          }
        })
      } catch (error) {
        this.disabled = false
        console.error(error)
      }
    },

    async updateDestination (destination) {
      try {
        this.$apollo.mutate({
          mutation: UPDATE_DESTINATION_MUTATION,
          variables: destination,
          update: (store, { data: { updateDestination } }) => {
            this.disabled = false

            let data = store.readQuery({ query: DESTINATIONS_QUERY })
            const key = findKey(data.destinations, { _id: destination._id })

            data.destinations[key] = {
              ...data.destinations[key],
              ...updateDestination
            }

            store.writeQuery({
              query: DESTINATIONS_QUERY,
              data
            })
          }
        })
      } catch (error) {
        this.disabled = false
        console.error(error)
      }
    },

    deleteDestination (destination) {
      try {
        this.$apollo.mutate({
          mutation: DELETE_DESTINATION_MUTATION,
          variables: { _id: destination._id },
          update: (store, response) => {
            this.disabled = false

            let data = store.readQuery({ query: DESTINATIONS_QUERY })
            const key = findKey(data.destinations, { _id: destination._id })

            data.destinations.splice(key, 1)

            store.writeQuery({
              query: DESTINATIONS_QUERY,
              data
            })
          }

        })
      } catch (error) {
        this.disabled = false
        console.error(error)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
//
</style>



