<template>
  <div class="content-wrapper">
    <!-- Tabs -->
    <div class="tabs is-boxed is-centered" v-if="$is(['admin', 'operateur', 'gestionnaire'])">
      <ul>
        <li
          v-for="(label, index) in statusTransactions"
          :key="index"
          :class="{'is-active': status === index, 'is-disabled': $apollo.queries.transactions.loading}"
          @click="changeStatus(index)"
        >
          <a>
            {{ label }}
          </a>
        </li>
      </ul>
    </div>

    <div class="tabs is-boxed is-centered" v-if="$is('livreur')">
      <ul>
        <li
          :key="'ALI'"
          :class="{'is-active': status === 'ALI', 'is-disabled': $apollo.queries.transactions.loading}"
          @click="changeStatus('ALI')"
        >
          <a> À livrer </a>
        </li>

        <li
          :key="'LIV'"
          :class="{'is-active': status === 'LIV', 'is-disabled': $apollo.queries.transactions.loading}"
          @click="changeStatus('LIV')"
        >
          <a> Livré </a>
        </li>
      </ul>
    </div>

    <b-table :data="transactionsFiltered" striped :loading="$apollo.queries.transactions.loading">
      <template slot-scope="props">
        <!-- ID -->
        <b-table-column label="#">
          <span v-if="props.row.vol">V-{{ props.row.vol._id }}</span>
          <span v-if="props.row.hotel">H-{{ props.row.hotel._id }}</span>
          <span v-if="props.row.visa">Vi-{{ props.row.visa._id }}</span>
          <span v-if="props.row.telex">Tx-{{ props.row.telex._id }}</span>
          <span v-if="props.row.assurance">A-{{ props.row.assurance._id }}</span>
        </b-table-column>

        <!-- PNR/Ref -->
        <b-table-column label="Ref.">
          <span v-if="props.row.vol">
            {{ props.row.vol.pnr.new || props.row.vol.pnr.original }}
          </span>

          <span v-if="props.row.hotel">
            {{ props.row.hotel.bookingRef || props.row.hotel.secondaryBookingRef }}
          </span>

          <span v-if="props.row.visa">
            {{ props.row.visa._id }}
          </span>

          <span v-if="props.row.telex">
            {{ props.row.telex._id }}
          </span>

          <span v-if="props.row.assurance">
            {{ props.row.assurance._id }}
          </span>
        </b-table-column>

        <!-- Client name -->
        <b-table-column label="Nom">
          <span v-if="props.row.vol">{{ props.row.vol.name }}</span>
          <span v-if="props.row.hotel">{{ props.row.hotel.firstName }} {{ props.row.hotel.lastName }}</span>
          <span v-if="props.row.visa">{{ props.row.visa.name }}</span>
        </b-table-column>

        <!-- Date livraison -->
        <b-table-column field="deliveryDate.scheduled" label="Date livraison">
          <span v-if="props.row.deliveryDate">
            {{ formatDate(props.row.deliveryDate.scheduled) }}
          </span>
          <span v-else>-</span>
        </b-table-column>

        <!-- Date livré -->
        <b-table-column field="deliveryDate.delivered" label="Date livré">
          <span v-if="props.row.deliveryDate">
            {{ formatDateTime(props.row.deliveryDate.delivered) }}
          </span>
          <span v-else>-</span>
        </b-table-column>

        <!-- Status -->
        <b-table-column label="Status">
          {{ statusTransactions[props.row.status] }}
          <span v-if="props.row.id">
            {{ props.row.printed ? 'Imprimé' : '' }}<br/>
            {{ props.row.delivered ? 'Livré' : '' }}
          </span>
        </b-table-column>

        <!-- Prix -->
        <b-table-column field="price.amount" label="Prix">
          {{ formatMoney(props.row.price.amount) }}
        </b-table-column>

        <!-- Actions -->
        <b-table-column label="Actions">
          <!-- Edit transaction -->
          <b-tooltip label="Editer transaction" position="is-top" v-if="$can('update', 'transactions')">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="promptEditTransaction(props.row)"
            >
              <mu-icon value="edit" :size="24"/>
            </a>
          </b-tooltip>

          <!-- Voucher -->
          <b-tooltip label="Imprimer voucher" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="printVoucher(props.row)"
            >
              <mu-icon value="printer" :size="24"/>
            </a>
          </b-tooltip>

          <!-- Recu -->
          <b-tooltip label="Imprimer reçu" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="printRecu(props.row)"
            >
              <mu-icon value="description" :size="24" />
            </a>
          </b-tooltip>

          <!-- Etiquette -->
          <b-tooltip label="Imprimer etiquette" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="printEtiquette(props.row)"
            >
              <mu-icon value="receipt" :size="24" />
            </a>
          </b-tooltip>

          <!-- Set printed -->
          <b-tooltip :label="props.row.printed ? 'Imprimé' : `Valider l'impression`" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="validatePrint(props.row)"
              :disabled="props.row.printed"
            >
              <mu-icon value="content_paste" :size="24" />
            </a>
          </b-tooltip>

          <!-- Validate deliverys -->
          <b-tooltip label="Valider livraison" position="is-top" v-if="props.row.status !== 'LIV'">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="validateDelivery(props.row)"
            >
              <mu-icon value="check" :size="24" />
            </a>
          </b-tooltip>
        </b-table-column>
      </template>
    </b-table>

    <!-- Pagination -->
    <b-pagination
      style="padding: 1.5rem 0"
      :total="transactions.total"
      :current.sync="page"
      :rounded="true"
      :per-page="pageSize"
      :order="'is-centered'"
    />

    <!-- Voucher vol -->
    <voucher-vol :open.sync="showVoucherVolModal" :transaction="transaction"/>

    <!-- Voucher hotel -->
    <voucher-hotel :open.sync="showVoucherHotelModal" :transaction="transaction"/>

    <!-- Voucher visa -->
    <voucher-visa :open.sync="showVoucherVisaModal" :transaction="transaction"/>

    <!-- Recu -->
    <recu :open.sync="showRecuModal" :transaction="transaction" />

    <!-- Etiquette -->
    <etiquette :open.sync="showEtiquetteModal" :transaction="transaction" />

    <!-- Edit transaction -->
    <edit-transaction
      :open.sync="showEditModal"
      :data="transaction"
      :variables="variables"
      :wilayas="wilayas"
      @update="editTransaction"
    />
  </div>
</template>

<script>
import { orderBy, findKey } from 'lodash'

import VoucherVol from '@/components/print/VoucherVol'
import VoucherHotel from '@/components/print/VoucherHotel'
import VoucherVisa from '@/components/print/VoucherVisa'
import Recu from '@/components/print/Recu'
import Etiquette from '@/components/print/Etiquette'
import EditTransaction from '@/components/transactions/EditTransaction'

import TRANSACTIONS_QUERY from '@/graphql/queries/transactionsQuery.gql'
import WILAYAS_QUERY from '@/graphql/queries/wilayasQuery.gql'
import EDIT_TRANSACTION_MUTATION from '@/graphql/mutations/editTransaction.gql'

export default {
  name: 'transactions',
  components: {
    VoucherVol,
    VoucherHotel,
    VoucherVisa,
    Recu,
    Etiquette,
    EditTransaction
  },

  data () {
    return {
      transactions: [],
      status: 'NEW',
      pageNumber: 1,
      pageSize: 10,

      wilayas: [],

      transaction: {},
      showVoucherVolModal: false,
      showVoucherHotelModal: false,
      showVoucherVisaModal: false,
      showRecuModal: false,
      showEtiquetteModal: false,

      showEditModal: false
    }
  },

  computed: {
    statusTransactions () { return this.$store.getters.statusTransactions },
    page: {
      get () {
        return this.pageNumber
      },

      set (value) {
        const page = parseInt(value, 10)
        if (page <= 0) {
          this.pageNumber = 1
        } else {
          this.pageNumber = page
        }
      }
    },

    variables () {
      if (!this.$user) {
        this.status = 0
      }

      if (this.$is('livreur')) {
        if (!['ALI', 'LIV'].includes(this.status)) {
          this.status = 'ALI'
        }
      }

      return {
        status: this.status,
        page: this.page,
        pageSize: this.pageSize
      }
    },

    transactionsFiltered () {
      if (this.transactions.data) {
        return orderBy(this.transactions.data.filter(el => {
          return el.status === this.status
        }), '_id')
      }

      return []
    }
  },

  apollo: {
    transactions: {
      query: TRANSACTIONS_QUERY,
      variables () {
        return this.variables
      },
      fetchPolicy: 'network-only'
    },

    wilayas: {
      query: WILAYAS_QUERY,
      fetchPolicy: 'network-only'
    }
  },

  methods: {
    changeStatus (status) {
      if (!this.$apollo.queries.transactions.loading) {
        this.status = status
      }
    },

    // Voucher
    printVoucher (transaction) {
      if (transaction.vol) {
        this.printVoucherVol(transaction)
      }

      if (transaction.hotel) {
        this.printVoucherHotel(transaction)
      }

      if (transaction.visa) {
        this.printVoucherVisa(transaction)
      }
    },

    printVoucherVol (transaction) {
      this.transaction = transaction
      this.showVoucherVolModal = true
    },

    printVoucherHotel (transaction) {
      this.transaction = transaction
      this.showVoucherHotelModal = true
    },

    printVoucherVisa (transaction) {
      this.transaction = transaction
      this.showVoucherVisaModal = true
    },

    // Recu
    printRecu (transaction) {
      this.transaction = transaction
      this.showRecuModal = true
    },

    printEtiquette (transaction) {
      this.transaction = transaction
      this.showEtiquetteModal = true
    },

    promptEditTransaction (transaction) {
      this.transaction = transaction
      this.showEditModal = true
    },

    validatePrint (transaction) {
      if (!transaction.printed) {
        this.$deleteModal.prompt({
          title: 'Valider impression',
          text: `Êtes-vous sûr de vouloir valider l'impression de ce dossier ?`,
          onConfirm: () => {
            this.editTransaction({ _id: transaction._id, printed: true })
          }
        })
      }
    },

    validateDelivery (transaction) {
      this.$deleteModal.prompt({
        title: 'Valider livraison',
        text: `Êtes-vous sûr de vouloir valider la livraison de ce dossier ?`,
        onConfirm: () => {
          this.editTransaction({ _id: transaction._id, status: 'LIV' })
        }
      })
    },

    async editTransaction (transaction) {
      try {
        await this.$apollo.mutate({
          mutation: EDIT_TRANSACTION_MUTATION,
          variables: {
            ...transaction
          },
          update: (store, { data: { editTransaction } }) => {
            let data = store.readQuery({
              query: TRANSACTIONS_QUERY,
              variables: { ...this.variables }
            })
            // Get dossier index
            const indexTransaction = findKey(data.transactions.data, {
              _id: transaction._id
            })

            data.transactions.data[indexTransaction] = editTransaction

            store.writeQuery({
              query: TRANSACTIONS_QUERY,
              variables: { ...this.variables },
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>



