<template>
  <div class="content-wrapper">
    <!-- Tabs -->
    <div class="tabs is-boxed is-centered">
      <ul>
        <li
          v-for="(label, index) in statusTelex"
          :key="index"
          :class="{'is-active': status === index, 'is-disabled': $apollo.queries.telexes.loading}"
          @click="changeStatus(index)"
        >
          <a>
            {{ label }}
          </a>
        </li>
      </ul>
    </div>

    <!-- Data -->
    <b-table :data="telexesFiltered" striped :loading="$apollo.queries.telexes.loading">
      <template slot-scope="props">
        <!-- ID -->
        <b-table-column field="_id" label="#">
          {{ props.row._id }}
        </b-table-column>

        <!-- Name -->
        <b-table-column field="name" label="Nom">
          {{ props.row.name }}
        </b-table-column>

        <!-- Contact -->
        <b-table-column field="contact" label="Contact" :visible="$can('read', 'contacts')" width="150">
          <a v-if="$can('update', 'contacts')" @click="editContacts(props.row)">
            {{ getFirstContactName(props.row) }} :
            <span style="white-space: nowrap">
              {{ getFirstContactNumber(props.row) }}
            </span>
          </a>

          <span v-else>
            {{ getFirstContactName(props.row) }} :
            <span style="white-space: nowrap">
              {{ getFirstContactNumber(props.row) }}
            </span>
          </span>
        </b-table-column>

        <!-- Destination -->
        <b-table-column field="destination" label="Destination">
          {{ props.row.destination }}
        </b-table-column>

        <!-- Date départ -->
        <b-table-column field="departureDate" label="Date départ">
          {{ formatDate(props.row.departureDate) }}
        </b-table-column>

        <!-- Durée voyage -->
        <b-table-column field="duration" label="Durée">
          {{ props.row.duration }} jours
        </b-table-column>

        <!-- Status -->
        <b-table-column field="stauts" label="Status">
          <a v-if="$can('update', 'telex')" @click.prevent="editStatus(props.row)">
            {{ statusTelex[props.row.status] }}
          </a>

          <span v-else>{{ statusTelex[props.row.status] }}</span>
        </b-table-column>

        <!-- Opérateur -->
        <b-table-column label="Op." :visible="$can('read', 'operateurs')">
          <a
            v-if="$can('update', 'operateurs')"
            @click.prevent="editOperator(props.row)"
          >{{ props.row.operator ? props.row.operator.name : '-' }}</a>

          <span v-else>{{ props.row.operator ? props.row.operator.name : '-' }}</span>
        </b-table-column>

        <!-- Prix -->
        <b-table-column label="Prix">
          <a @click="editTransactions(props.row)">
            <span v-if="props.row.transactions.length">
              {{ formatMoney(props.row.transactions[0].price.amount) }}
            </span>

            <span v-else>-</span>
          </a>
        </b-table-column>
      </template>
    </b-table>

    <!-- Pagination -->
    <b-pagination
      style="padding: 1.5rem 0"
      :total="telexes.total"
      :current.sync="page"
      :rounded="true"
      :per-page="pageSize"
      :order="'is-centered'"
    />

    <!-- Edit status dialog -->
    <status
      :open.sync="statusDialog.open"
      :dossier="statusDialog.dossier"
      type="telex"
      @update="updateDossier"
    />

    <!-- Edit operator dialog -->
    <operator
      :open.sync="operatorDialog.open"
      :dossier="operatorDialog.dossier"
      type="telex"
      @update="updateDossier"
    />

    <!-- Edit contacts dialog -->
    <edit-contacts
      :open.sync="contactsDialog.open"
      :dossier="contactsDialog.dossier"
      :variables="variables"
      type="telex"
    />

    <!-- Edit transactions dialog -->
    <edit-transactions
      :open.sync="transactionsDialog.open"
      :dossier="transactionsDialog.dossier"
      :variables="variables"
      :wilayas="wilayas"
      type="telex"
    />
  </div>
</template>

<script>
import { findKey, orderBy } from 'lodash'

import Status from '@/components/dossiers/Status'
import Operator from '@/components/dossiers/Operator'
import EditContacts from '@/components/edit-contacts'
import EditTransactions from '@/components/transactions/EditTransactions'

import TELEXES_QUERY from '@/graphql/queries/telexesQuery.gql'
import WILAYAS_QUERY from '@/graphql/queries/wilayasQuery.gql'
import DOSSIER_TELEX_MUTATION from '@/graphql/mutations/updateDossierTelex.gql'

export default {
  name: 'telex',
  components: {
    Status,
    Operator,
    EditContacts,
    EditTransactions
  },

  data () {
    return {
      telexes: [],
      status: 'NEW',
      pageNumber: 1,
      pageSize: 5,

      statusDialog: { open: false, dossier: {} },
      operatorDialog: { open: false, dossier: {} },
      contactsDialog: { open: false, dossier: {} },
      transactionsDialog: { open: false, dossier: {} }
    }
  },

  apollo: {
    telexes: {
      query: TELEXES_QUERY,
      variables () { return this.variables },
      fetchPolicy: 'network-only'
    },

    wilayas: WILAYAS_QUERY
  },

  computed: {
    statusTelex () { return this.$store.getters.statusTelex },

    page: {
      get () {
        return this.pageNumber
      },

      set (value) {
        const page = parseInt(value, 10)
        if (page <= 0) {
          this.pageNumber = 1
        } else {
          this.pageNumber = page
        }
      }
    },

    variables () {
      return {
        status: this.status,
        page: this.page,
        pageSize: this.pageSize
      }
    },

    telexesFiltered () {
      if (this.telexes.data) {
        return orderBy(this.telexes.data.filter(el => {
          return el.status === this.status
        }), '_id')
      }

      return []
    }
  },

  methods: {
    editStatus (dossier) { this.statusDialog = { dossier, open: true } },
    editOperator (dossier) { this.operatorDialog = { dossier, open: true } },
    editContacts (dossier) { this.contactsDialog = { dossier, open: true } },
    editTransactions (dossier) { this.transactionsDialog = { dossier, open: true } },

    changeStatus (status) {
      if (!this.$apollo.queries.telexes.loading) {
        this.status = status
      }
    },

    getFirstContactName (dossier) {
      try {
        return dossier.contacts[0].name
      } catch (error) {
        return '-'
      }
    },

    getFirstContactNumber (dossier) {
      try {
        return dossier.contacts[0].phones[0].number
      } catch (error) {
        return '-'
      }
    },

    async updateDossier (params) {
      this.done = false

      try {
        await this.$apollo.mutate({
          mutation: DOSSIER_TELEX_MUTATION,
          variables: params,
          update: (store, { data: { updateDossierTelex } }) => {
            let data = store.readQuery({
              query: TELEXES_QUERY,
              variables: { ...this.variables }
            })
            const key = findKey(data.telexes.data, { _id: params._id })

            data.telexes.data[key] = {
              ...data.telexes.data[key],
              ...updateDossierTelex
            }

            store.writeQuery({
              query: TELEXES_QUERY,
              variables: { ...this.variables },
              data
            })
          }
        })
        this.statusDialog.open = false
        this.done = true
      } catch (error) {
        console.error(error)
        this.statusDialog.open = false
        this.done = true
      }
    }
  }
}
</script>

<style lang="scss" scoped>
//
</style>



