<template>
  <mu-dialog :open="open" @close="close" title="Versement deposit">
    <div>
      <mu-text-field
        label="Montant"
        v-model="deposit.amount"
        min="0"
        type="number"
      />

      <br />

      <mu-text-field
        label="Commentaire"
        multiLine
        v-model="deposit.comment"
      />
    </div>

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
        />

        <!-- Button sauvegarder -->
        <mu-raised-button
          label="Sauvegarder"
          @click.prevent="save"
          style="margin: 0 1rem"
          primary
        />
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
export default {
  name: 'increment-deposit',
  props: ['open', 'depositId'],

  data () {
    return {
      deposit: {
        amount: null,
        comment: null
      }
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    async save () {
      this.$emit('update', { ...this.deposit, depositId: this.depositId })
      this.deposit = { amount: null, comment: null }
      this.close()
    }
  }
}
</script>



