<template>
  <mu-dialog :open="open" @close="close" title="Zone livraison" v-if="open">
    <!-- Wilaya -->
    <mu-select-field
      v-model="wilayaId"
      label="Wilaya"
      labelFloat
      style="vertical-align: bottom"
      :maxHeight="250"
      :disabled="!newZone"
      @input="updateCommunes"
    >
      <mu-menu-item
        v-for="(w, index) in wilayas" :key="index"
        :value="w._id" :title="w.name"
      />
    </mu-select-field>

    <!-- Communes -->
    <mu-select-field
      v-model="communesIds"
      multiple
      label="Communes"
      labelFloat
      style="vertical-align: bottom"
      separator="; "
      :maxHeight="250"
    >
      <mu-menu-item
        v-for="(commune, index) in wilaya.communes" :key="index"
        :value="commune._id" :title="commune.name"
      />
    </mu-select-field>

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
        />

        <!-- Button sauvegarder -->
        <mu-raised-button
          label="Sauvegarder"
          @click.prevent="save"
          style="margin: 0 1rem"
          primary
        />
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
import { find, findKey } from 'lodash'
import ADD_DELIVERY_ZONE from '@/graphql/mutations/addDeliveryZone.gql'
import EDIT_DELIVER_ZONE from '@/graphql/mutations/editDeliveryZone.gql'
import DELIVERY_ZONES_QUERY from '@/graphql/queries/deliveryZonesQuery.gql'

export default {
  name: 'edit-delivery-zone',
  props: ['open', 'zone', 'wilayas', 'newZone'],

  data () {
    return {
      wilaya: {},
      wilayaId: 0,
      communesIds: []
    }
  },

  watch: {
    open (value) {
      if (value) {
        if (Object.keys(this.zone).length) {
          this.wilaya = find(this.wilayas, { _id: this.zone.wilaya._id })
          this.wilayaId = this.wilaya._id
          this.communesIds = this.zone.communes.map(el => el._id)
        }
      }
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    updateCommunes () {
      this.wilaya = find(this.wilayas, { _id: this.wilayaId })
    },

    save () {
      if (this.zone._id) {
        this.updateZone()
      } else {
        this.addZone()
      }

      this.close()
    },

    addZone () {
      this.$apollo.mutate({
        mutation: ADD_DELIVERY_ZONE,
        variables: {
          wilaya: this.wilayaId,
          communes: this.communesIds
        },
        update: (store, { data: { addDeliveryZone } }) => {
          let data = store.readQuery({ query: DELIVERY_ZONES_QUERY })

          data.deliveryZones.push(addDeliveryZone)

          store.writeQuery({
            query: DELIVERY_ZONES_QUERY,
            data
          })
        }
      })
    },

    updateZone () {
      this.$apollo.mutate({
        mutation: EDIT_DELIVER_ZONE,
        variables: {
          _id: this.zone._id,
          wilaya: this.wilayaId,
          communes: this.communesIds
        },
        update: (store, { data: { updateDeliveryZone } }) => {
          let data = store.readQuery({ query: DELIVERY_ZONES_QUERY })

          // Get user index
          const indexDeliveryZone = findKey(data.deliveryZones, {
            _id: this.zone._id
          })

          data.deliveryZones[indexDeliveryZone] = updateDeliveryZone

          store.writeQuery({
            query: DELIVERY_ZONES_QUERY,
            data
          })
        }
      })
    }
  }
}
</script>

<style lang="scss">
  .mu-select-field {
    max-width: 256px;
  }
</style>



