<template>
  <div class="content-wrapper">
    <!-- Search field -->
    <div style="padding-bottom: 1.3rem">
      <mu-text-field
        icon="search"
        hintText="Chercher"
        v-model="searchQuery"
        fullWidth
      ></mu-text-field>
    </div>

    <!-- Tabs -->
    <div class="tabs is-boxed is-centered">
      <ul>
        <li
          v-for="(label, index) in statusVols"
          :key="index"
          :class="{'is-active': status === index, 'is-disabled': $apollo.queries.vols.loading}"
          @click="changeStatus(index)"
        >
          <a>
            {{ label }}
          </a>
        </li>
      </ul>
    </div>

    <!-- Data -->
    <b-table :data="volsFiltered" striped :loading="$apollo.queries.vols.loading">
      <template slot-scope="props">
        <!-- ID -->
        <b-table-column field="_id" label="#">
          {{ props.row._id }}
        </b-table-column>

        <!-- PNR -->
        <b-table-column field="pnr" label="PNR">
          <a @click.prevent="openPnrDialog(props.row)">
            {{ props.row.pnr.new || props.row.pnr.original }}
          </a>
        </b-table-column>

        <!-- Delai emission -->
        <b-table-column field="emissionLimit" label="Délai d'emission">
          <a @click.prevent="editEmissionLimit(props.row)">
            {{ formatDateTime(props.row.emissionLimit) }}
          </a>
        </b-table-column>

        <!-- Status -->
        <b-table-column field="stauts" label="Status">
          <a v-if="$can('update', 'vols')" @click.prevent="editStatus(props.row)">
            {{ statusVols[props.row.status] }}
          </a>

          <span v-else>{{ statusVols[props.row.status] }}</span>
        </b-table-column>

        <!-- Opérateur -->
        <b-table-column label="Op." :visible="$can('read', 'operateurs')">
          <a
            v-if="$can('update', 'operateurs')"
            @click.prevent="editOperator(props.row)"
          >{{ props.row.operator ? props.row.operator.name : '-' }}</a>

          <span v-else>{{ props.row.operator ? props.row.operator.name : '-' }}</span>
        </b-table-column>

        <!-- Contact -->
        <b-table-column field="contact" label="Contact" :visible="$can('read', 'contacts')">
          <a v-if="$can('update', 'contacts')" @click="editContacts(props.row)">
            {{ props.row.contacts.length ? props.row.contacts[0].name : '-' }} :
            <span style="white-space: nowrap">
              {{
                props.row.contacts.length
                ? (props.row.contacts[0].phones && props.row.contacts[0].phones.length
                  ? props.row.contacts[0].phones[0].number : '-')
                : '-'
              }}
            </span>
          </a>

          <span v-else>
            {{ props.row.contacts.length ? props.row.contacts[0].name : '-' }} :
            <span style="white-space: nowrap">
              {{
                props.row.contacts.length
                ? (props.row.contacts[0].phones && props.row.contacts[0].phones.length
                  ? props.row.contacts[0].phones[0].number : '-')
                : '-'
              }}
            </span>
          </span>
        </b-table-column>

        <!-- Date création -->
        <b-table-column field="creationDate" label="Date création">
          {{ formatDate(props.row.creationDate) }}
        </b-table-column>

        <!-- Date dernière modification -->
        <b-table-column field="updateDate" label="Dernière modif.">
          {{ formatDate(props.row.updateDate) }}
        </b-table-column>

        <!-- Nombre de changements -->
        <!-- <b-table-column field="numberOfChanges" label="Nbr. chang.">
          {{ props.row.numberOfChanges }}
        </b-table-column> -->

        <!-- Nombre de réservations/passagers -->
        <!-- <b-table-column label="Nbr. résa.">
          {{ props.row.passengers.length || 0 }}
        </b-table-column> -->

        <!-- Date départ -->
        <b-table-column label="Date départ">
          {{ formatDateTime(props.row.itineraries[0].departure.date) }}
        </b-table-column>

        <!-- Prix -->
        <b-table-column label="Prix">
          <a @click="editTransactions(props.row)">
            <span v-if="props.row.transactions.length">
              {{ formatMoney(props.row.transactions[0].price.amount) }}
            </span>

            <span v-else>-</span>
          </a>
        </b-table-column>

        <!-- Actions -->
        <b-table-column label="Actions" :visible="!$can('operateurs', 'update')">
          <!-- Edit button -->
          <b-tooltip
            label="Actions PNR"
            position="is-top"
            v-if="!$can('operateurs', 'update')"
          >
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="editDialog(props.row)"
            >
              <mu-icon value="menu" :size="24" />
            </a>
          </b-tooltip>

          <b-tooltip
            label="S'assigner le dossier"
            position="is-top"
            v-if="!$can('operateurs', 'update')"
          >
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="assignDossier(props.row)"
              :disabled="props.row.operator && $user._id === props.row.operator._id"
            >
              <mu-icon value="check" :size="24" />
            </a>
          </b-tooltip>
        </b-table-column>
      </template>
    </b-table>

    <!-- Pagination -->
    <b-pagination
      style="padding: 1.5rem 0"
      :total="vols.total"
      :current.sync="page"
      :rounded="true"
      :per-page="pageSize"
      :order="'is-centered'"
    />

    <!-- PNR Info dialog -->
    <pnr :open.sync="pnrDialog.open" :dossier="pnrDialog.dossier"/>

    <!-- Edit delai emission dialog -->
    <emission-limit
      :open.sync="emissionLimitDialog.open"
      :dossier="emissionLimitDialog.dossier"
      @update="updateDossier"
    />

    <!-- Edit status dialog -->
    <status
      :open.sync="statusDialog.open"
      :dossier="statusDialog.dossier"
      type="vol"
      @update="updateDossier"
    />

    <!-- Edit operator dialog -->
    <operator
      :open.sync="operatorDialog.open"
      :dossier="operatorDialog.dossier"
      type="vol"
      @update="updateDossier"
    />

    <!-- Edit contacts dialog -->
    <edit-contacts
      :open.sync="contactsDialog.open"
      :dossier="contactsDialog.dossier"
      :variables="variables"
      type="vol"
    />

    <!-- Edit transactions dialog -->
    <edit-transactions
      :open.sync="transactionsDialog.open"
      :dossier="transactionsDialog.dossier"
      :variables="variables"
      :wilayas="wilayas"
      type="vol"
    />

    <!-- Edit PNR dialog -->
    <edit-vol-dialog
      :open.sync="editVolDialog.open"
      :dossier="editVolDialog.dossier"
    />
  </div>
</template>

<script>
import { findKey, orderBy } from 'lodash'

import Pnr from '@/components/vols/Pnr'
import EmissionLimit from '@/components/vols/EmissionLimit'
import Status from '@/components/dossiers/Status'
import Operator from '@/components/dossiers/Operator'
import EditContacts from '@/components/edit-contacts'
import EditTransactions from '@/components/transactions/EditTransactions'
import EditVolDialog from '@/components/vols/EditDialog'

import VOLS_QUERY from '@/graphql/queries/volsQuery.gql'
import DOSSIER_VOL_MUTATION from '@/graphql/mutations/dossierVol.gql'
import WILAYAS_QUERY from '@/graphql/queries/wilayasQuery.gql'

export default {
  name: 'vols',
  components: {
    Pnr,
    EmissionLimit,
    Status,
    Operator,
    EditContacts,
    EditTransactions,
    EditVolDialog
  },

  data () {
    return {
      searchQuery: '',
      searchType: 'pnr',
      wilayas: [],

      pnrDialog: { open: false, dossier: {} },
      emissionLimitDialog: { open: false, dossier: {} },
      statusDialog: { open: false, dossier: {} },
      operatorDialog: { open: false, dossier: {} },
      contactsDialog: { open: false, dossier: {} },
      transactionsDialog: { open: false, dossier: {} },
      editVolDialog: { open: false, dossier: {} },

      vols: [],
      status: 'NEW',
      pageNumber: 1,
      pageSize: 5
    }
  },

  computed: {
    statusVols () { return this.$store.getters.status },

    page: {
      get () {
        return this.pageNumber
      },

      set (value) {
        const page = parseInt(value, 10)
        if (page <= 0) {
          this.pageNumber = 1
        } else {
          this.pageNumber = page
        }
      }
    },

    variables () {
      return {
        status: this.status,
        page: this.page,
        pageSize: this.pageSize
      }
    },

    volsFiltered () {
      if (this.vols.data) {
        return orderBy(this.vols.data.filter(el => {
          return el.status === this.status
        }), '_id')
      }

      return []
    }
  },

  apollo: {
    vols: {
      query: VOLS_QUERY,
      variables () {
        return { ...this.variables }
      },
      fetchPolicy: 'network-only'
    },

    wilayas: {
      query: WILAYAS_QUERY,
      fetchPolicy: 'network-only'
    }
  },

  methods: {
    openPnrDialog (dossier) { this.pnrDialog = { dossier, open: true } },
    editEmissionLimit (dossier) { this.emissionLimitDialog = { dossier, open: true } },
    editStatus (dossier) { this.statusDialog = { dossier, open: true } },
    editOperator (dossier) { this.operatorDialog = { dossier, open: true } },
    editContacts (dossier) { this.contactsDialog = { dossier, open: true } },
    editTransactions (dossier) { this.transactionsDialog = { dossier, open: true } },
    editDialog (dossier) { this.editVolDialog = { dossier, open: true } },

    changeStatus (status) {
      if (!this.$apollo.queries.vols.loading) {
        this.status = status
      }
    },

    assignDossier (dossier) {
      if (dossier.operator) {
        if (this.$user._id === dossier.operator._id) return
      }

      this.$deleteModal.prompt({
        title: 'Assigner dossier',
        text: `Êtes-vous sûr de vouloir vous assigner ce dossier ?`,
        onConfirm: () => {
          this.updateDossier({
            pnr: dossier.pnr.original,
            operator: this.$user._id
          })
        }
      })
    },

    async updateDossier (params) {
      try {
        await this.$apollo.mutate({
          mutation: DOSSIER_VOL_MUTATION,
          variables: params,
          update: (store, { data: { editDossierVol } }) => {
            let data = store.readQuery({
              query: VOLS_QUERY,
              variables: { ...this.variables }
            })
            const key = findKey(data.vols.data, { pnr: { original: params.pnr } })

            data.vols.data[key] = {
              ...data.vols.data[key],
              ...editDossierVol
            }

            store.writeQuery({
              query: VOLS_QUERY,
              variables: { ...this.variables },
              data
            })
          }
        })
        this.emissionLimitDialog.open = false
        this.statusDialog.open = false
      } catch (error) {
        console.error(error)
        this.emissionLimitDialog.open = false
        this.statusDialog.open = false
      }
    }
  }
}
</script>



