<template>
  <div class="content-wrapper">
    <!-- New commission button -->
    <div style="text-align: right">
      <mu-flat-button label="Ajouter commission" @click.prevent="editCommission({})" v-if="$can('create', 'commissions')" />
    </div>

    <b-table :data="commissions" striped :loading="$apollo.queries.commissions.loading">
      <template slot-scope="props">
        <!-- Type -->
        <b-table-column field="type" label="Type">
          {{ props.row.type }}
        </b-table-column>

        <!-- Code -->
        <b-table-column field="code" label="Code">
          {{ props.row.code }}
        </b-table-column>

        <!-- Initial -->
        <b-table-column field="initial" label="Commission">
          {{ props.row.initial }}%
        </b-table-column>

        <!-- Added -->
        <b-table-column field="added" label="Ajouté">
          {{ formatMoney(props.row.added) }} DZD
        </b-table-column>

        <!-- Options -->
        <b-table-column label="Options">
          <!-- Edit -->
          <b-tooltip label="Editer" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="editCommission(props.row)"
              v-if="$can('update', 'commissions')"
            >
              <mu-icon value="edit" :size="18"/>
            </a>
          </b-tooltip>

          <b-tooltip label="Supprimer" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="promptRemoveCommission(props.row)"
              v-if="$can('delete', 'commissions')"
            >
              <mu-icon value="delete" :size="18"/>
            </a>
          </b-tooltip>
        </b-table-column>
      </template>
    </b-table>

    <edit-commission :open.sync="commModal" :commission="commission" />
  </div>
</template>

<script>
import { findKey } from 'lodash'
import EditCommission from '@/components/edit-commission'
import COMMISSIONS_QUERY from '@/graphql/queries/commissionsQuery.gql'
import REMOVE_COMMISSION_MUTATION from '@/graphql/mutations/deleteCommission.gql'

export default {
  name: 'commissions',
  components: { EditCommission },

  data () {
    return {
      commissions: [],
      commission: {},
      commModal: false
    }
  },

  apollo: {
    commissions: {
      query: COMMISSIONS_QUERY,
      fetchPolicy: 'network-only'
    }
  },

  methods: {
    editCommission (comm) {
      this.commission = comm
      this.commModal = true
    },

    promptRemoveCommission (comm) {
      this.$deleteModal.prompt({
        title: 'Supprimer commission',
        onConfirm: () => {
          this.removeCommission(comm._id)
        }
      })
    },

    async removeCommission (_id) {
      try {
        await this.$apollo.mutate({
          mutation: REMOVE_COMMISSION_MUTATION,
          variables: { _id },
          update (store, { response }) {
            let data = store.readQuery({ query: COMMISSIONS_QUERY })
            // Get dossier index
            const indexCommission = findKey(data.commissions, { _id })

            data.commissions.splice(indexCommission, 1)

            store.writeQuery({
              query: COMMISSIONS_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>



