<template>
  <mu-dialog :open="open" @close="close" title="Transaction" v-if="Object.keys(transaction).length">
    <div style="padding: 1rem 0">
      <!-- Status -->
      <mu-select-field
        label="Status"
        v-model="transaction.status"
        style="vertical-align: bottom"
        labelFloat
      >
        <mu-menu-item
          v-for="(status, index) in statusTransactions"
          :key="index"
          :value="index"
          :title="status"
        />
      </mu-select-field>

      <!-- Stauts indicator -->
      <div style="display: inline-block; padding: 0 0 1rem 1rem">
        <!-- Status: -->

        <small
          :class="{ disabled: !transaction.printed, 'bolden': transaction.printed }"
        >Imprimé</small>

        <small
          :class="{ disabled: !transaction.delivered, 'bolden': transaction.printed }"
        >Livré</small>
      </div>

      <br/>

      <mu-select-field
        label="Type"
        v-model="transaction.type"
        style="vertical-align: bottom"
        labelFloat
      >
        <mu-menu-item
          v-for="(type, index) in ['livraison', 'locale', 'ccp']" :key="index"
          :title="type" :value="type"
        />
      </mu-select-field>

      <b-collapse class="panel" :open="true">
        <div slot="trigger" class="panel-heading">
          <strong>Prix</strong>
        </div>

        <div class="panel-block" style="display: block">
          <!-- Price field -->
          <mu-text-field
            type="number"
            label="Prix"
            v-model="transaction.price.amount"
            labelFloat
          />

          <!-- Commission field -->
          <mu-text-field
            type="number"
            label="Commission"
            v-model="transaction.price.commission"
            labelFloat
          />

          <br/>

          <!-- Price type field -->
          <mu-select-field
            label="Type transaction"
            v-model="transaction.price.type"
            style="vertical-align: bottom"
            labelFloat
          >
            <mu-menu-item :value="1" title="Paiement / Changement"/>
            <mu-menu-item :value="-1" title="Remboursement"/>
          </mu-select-field>

          <!-- Delivery price field -->
          <mu-text-field
            type="number"
            label="Prix livraison"
            v-model="transaction.price.deliveryPrice"
            labelFloat
          />
        </div>
      </b-collapse>

      <b-collapse class="panel" :open="true">
        <div slot="trigger" class="panel-heading">
          <strong>Livraison</strong>
        </div>

        <div class="panel-block" style="display: block">
          <!-- Scheduled delivery dates -->
          <fr-date-picker
            label="Date de livraison"
            v-model="transaction.deliveryDate.scheduled"
          />

          <!-- Delivered date -->
          <fr-date-picker
            label="Date livréée"
            :disabled="true"
            v-model="transaction.deliveryDate.delivered"
          />

          <br/>

          <!-- Person name -->
          <mu-text-field
            v-model="transaction.address.name"
            label="Nom du destinataire"
            labelFloat
          />

          <!-- Scheduled delivery time of day -->
          <mu-select-field
            style="vertical-align: bottom"
            v-model="transaction.deliveryDate.timeOfDay"
            label="Heure"
            labelFloat
          >
            <mu-menu-item
              :value="parseInt(0, 10)"
              title="Matinée"
            />

            <mu-menu-item
              :value="parseInt(1, 10)"
              title="Après-midi"
            />
          </mu-select-field>

          <!-- Address -->
          <h2 style="padding-top: 1rem; margin-bottom: 0" class="subtitle">Adresse:</h2>
          <!-- Wilaya -->
          <mu-select-field
            v-model="transaction.address.wilaya"
            label="Wilaya"
            labelFloat
            style="vertical-align: bottom"
            :maxHeight="250"
          >
            <mu-menu-item
              v-for="(w, index) in wilayasOrdered" :key="index"
              :value="parseInt(w._id, 10)" :title="`${w.code} - ${w.name}`"
            />
          </mu-select-field>

          <!-- Communes -->
          <mu-select-field
            v-model="transaction.address.commune"
            label="Communes"
            labelFloat
            style="vertical-align: bottom"
            separator="; "
            :maxHeight="250"
          >
            <mu-menu-item
              v-for="(commune, index) in communes(transaction.address.wilaya)" :key="index"
              :value="parseInt(commune._id, 10)" :title="commune.name"
            />
          </mu-select-field>

          <br/>

          <!-- Address -->
          <mu-text-field
            v-model="transaction.address.address"
            label="Adresse"
            multiLine
            :rows="3"
            labelFloat
          />
        </div>
      </b-collapse>

      <div style="display: flex">
        <!-- Save button -->
        <div style="flex: 9">
          <mu-raised-button primary label="Sauvegarder" @click="save(transaction)"/>
        </div>

        <!-- Close button -->
        <div style="flex: 1">
          <mu-raised-button label="Fermer" @click="close" />
        </div>
      </div>
    </div>
  </mu-dialog>
</template>

<script>
import { cloneDeep, orderBy, isEmpty, find } from 'lodash'
import FrDatePicker from '@/components/datepicker'

export default {
  name: 'edit-transaction',
  props: ['open', 'data', 'wilayas'],
  components: { FrDatePicker },

  data () {
    return {
      transaction: {}
    }
  },

  computed: {
    statusTransactions () { return this.$store.getters.statusTransactions },
    wilayasOrdered () {
      return orderBy(this.wilayas, 'code')
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    save (transaction) {
      this.$emit('update', transaction)
      this.close()
    },

    communes (wilaya) {
      if (!isEmpty(this.wilayas) && wilaya) {
        return orderBy(find(this.wilayas, { _id: wilaya }).communes, 'name')
      }
    }
  },

  watch: {
    open (value) {
      if (value) {
        this.transaction = cloneDeep(this.data)

        if (this.transaction.deliveryDate && !isEmpty(this.transaction.deliveryDate)) {
          if (this.transaction.deliveryDate.delivered) {
            this.transaction.deliveryDate.delivered = this.formatDateToISO(this.transaction.deliveryDate.delivered)
          }

          if (this.transaction.deliveryDate.scheduled) {
            this.transaction.deliveryDate.scheduled = this.formatDateToISO(this.transaction.deliveryDate.scheduled)
          }
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .disabled {
    text-decoration: line-through;
    color: #dddddd;
  }

  .bolden {
    font-weight: bold;
  }
</style>



