<template>
  <mu-dialog :open="open" @close="close">
    <div class="visa-upload">
      <form @submit.prevent="update" style="margin: 1rem auto">
        <b-field>
          <b-upload
            v-model="dropFiles"
            drag-drop
          >
            <section class="section">
              <div class="content has-text-centered">
                <p>
                  <b-icon
                    icon="upload"
                    size="is-large"
                  ></b-icon>
                </p>
                <p>Drop your files here or click to upload</p>
              </div>
            </section>
          </b-upload>
        </b-field>

        <div class="tags">
          <span v-for="(file, index) in dropFiles"
            :key="index"
            class="tag is-primary"
          >
            {{file.name}}
            <button class="delete is-small"
              type="button"
              @click="deleteDropFile(index)">
            </button>
          </span>
        </div>
      </form>
    </div>

    <footer>
      <div style="padding: 1rem; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          secondary label="Fermer"
          @click.prevent="close"
        ></mu-raised-button>

        <!-- Button valider -->
        <mu-raised-button
          primary
          label="Valider"
          style="margin: 0 .5rem"
          @click.prevent="update"
          :disabled="!done || !dropFiles.length"
        ></mu-raised-button>
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
export default {
  name: 'visa-upload',
  props: ['open', 'dossier', 'done'],

  data () {
    return {
      dropFiles: [],
      imageDialog: false
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    deleteDropFile (index) {
      this.dropFiles.splice(index, 1)
    },

    update () {
      this.$emit('update', {
        _id: this.dossier._id,
        visa: this.dropFiles[0]
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.visa-upload {
  padding: 1rem;
  text-align: center;
}

.visa-image {
  max-width: 500px;
  max-height: 300px;
}
</style>



