<template>
  <mu-dialog :open="open" @close="close" v-if="Object.keys(transaction).length">
    <div id="etiquette">
      <!-- Name -->
      <div class="is-uppercase">{{ transaction.address.name || '-' }}</div>

      <!-- ID -->
      <div>N&deg; de réservation: {{ id || '-' }}</div>

      <!-- Phone #1 -->
      <div>{{ getPhoneNumber(0) }}</div>

      <!-- Address -->
      <div>{{ transaction.address.address }}</div>

      <!-- Prix in letters -->
      <!-- <div class="is-capitalized">{{ numberToWords(transaction.price.amount) || '-' }} Dinars Algériens</div> -->
    </div>

    <mu-flat-button class="noprint" label="Imprimer" slot="actions" secondary @click="print()"/>
    <mu-flat-button class="noprint" label="Fermer" slot="actions" primary @click="close"/>
  </mu-dialog>
</template>

<script>
import printJS from 'print-js'

export default {
  name: 'etiquette',
  props: ['open', 'transaction'],

  data () {
    return {
      background: false
    }
  },

  computed: {
    id () {
      if (this.transaction.vol) return `V-${this.transaction.vol._id}`
      if (this.transaction.hotel) return `H-${this.transaction.hotel._id}`
      if (this.transaction.visa) return `Vi-${this.transaction.visa._id}`
      if (this.transaction.telex) return `Tx-${this.transaction.telex._id}`
      if (this.transaction.assurance) return `A-${this.transaction.assurance._id}`
    },

    dossier () {
      if (this.transaction.vol) return this.transaction.vol
      if (this.transaction.hotel) return this.transaction.hotel
      if (this.transaction.visa) return this.transaction.visa
      if (this.transaction.telex) return this.transaction.telex
      if (this.transaction.assurance) return this.transaction.assurance
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    getPhoneNumber (index) {
      if (!index) index = 0

      try {
        const number = this.dossier.contacts[0].phones[index].number
        return number
      } catch (error) {
        return null
      }
    },

    print () {
      printJS({
        printable: 'etiquette',
        type: 'html',
        font: 'Arial',
        honorColor: true,
        targetStyles: ['*'],
        style: `
          @page {
            /*size: A4;*/
            margin: 0;
            padding: 0;
          }

          #etiquette {
            min-height: 45mm !important;
            min-width: 105mm !important;
          }
        `
      })
    }
  }
}
</script>

<style lang="scss">
    #etiquette {
    margin: 1rem;
    padding: 2rem;
    color: #000;
    font-weight: 500;
    line-height: 1cm;

    .panel-block {
      align-items: baseline;
    }

    table {
      border: 1px solid black;
      width: 100%;

      td, th {
        border: 1px solid black;
        padding: .5rem;
      }
    }

    .itinerary {
      padding: .5rem 0;
    }
  }
</style>



