<template>
  <mu-dialog :open="open" @close="close" id="voucher-vol-container" v-if="Object.keys(transaction).length && transaction.vol">
    <!-- Tabs -->
    <b-tabs
      v-if="transaction.vol.passengers.length > 1"
      style="padding-top: 1rem"
      class="noprint"
      v-model="passengerId"
      type="is-boxed"
      position="is-centered"
    >
      <b-tab-item
        v-for="(passenger, index) in transaction.vol.passengers"
        :key="index"
        :label="`Passager #${index + 1}`"
      ></b-tab-item>
    </b-tabs>

    <div style="padding: 1rem">
      <mu-switch v-model="background" label="Image de fond"></mu-switch>
    </div>

    <div id="voucher-vol">
      <!-- Passenger / User info -->
      <table>
        <thead>
          <tr>
            <th>Nom</th>
            <th>N° Tel</th>
            <th>N° Res</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <!-- Nom -->
            <td>{{ transaction.vol.passengers[passengerId].name }} {{ transaction.vol.passengers[passengerId].lastName }}</td>

            <!-- Telephone -->
            <td>
              <span v-if="transaction.vol.contacts.length">
                {{ transaction.vol.contacts[0].phones[0].number }}
              </span>
            </td>

            <!-- Booking ref. -->
            <td> {{ transaction.vol.pnr.new || transaction.vol.pnr.original }} </td>
          </tr>
        </tbody>
      </table>

      <!-- Itineraires / Itineraries -->
      <div id="itineraries" style="padding-top: 3.5cm">
        <div
          class="itinerary"
          v-for="(itinerary, index) in transaction.vol.itineraries"
          :key="index"
        >
          <div class="panel">
            <div class="panel-heading">
              {{ itinerary.airline.name }}
              [{{ itinerary.airline.code }} {{ itinerary.flight.number }}]
            </div>

            <div class="panel-block">
              <!-- Departure -->
              <div class="departure has-text-centered" style="flex: 5">
                <!-- <mu-icon value="flight_takeoff" :size="24"/> -->
                <p style="font-weight: bold">Départ:</p>
                <p>{{itinerary.departure.airport.city}} / {{ itinerary.departure.airport.name }}</p>
                <p>{{ formatDateTime(itinerary.departure.date, itinerary.departure.airport.tz) }}</p>
              </div>

              <!-- Duration -->
              <div class="has-text-centered" style="flex: 3">
                <p>Classe : {{ itinerary.flight.class }}</p>
                <p>Bagage : {{ itinerary.baggage }}</p>
                <p>Avion : {{ itinerary.aircraft }}</p>
                <p>Durée: {{ itinerary.duration }} </p>
              </div>

              <!-- Arrival -->
              <div class="departure has-text-centered" style="flex: 5">
                <!-- <mu-icon value="flight_land" :size="24"/> -->
                <p style="font-weight: bold">Arrivée:</p>
                <p>{{itinerary.arrival.airport.city}} / {{ itinerary.arrival.airport.name }}</p>
                <p>{{ formatDateTime(itinerary.arrival.date, itinerary.arrival.airport.tz) }}</p>
              </div>
            </div>
          </div>
          <div v-if="((index + 1 ) === 3)" style="page-break-after: always"></div>
        </div>
      </div>
    </div>

    <!-- Action buttons -->
    <span slot="title">Impression voucher vol</span>
    <span slot="title">
      <mu-flat-button class="noprint" label="Imprimer" secondary @click="print()"/>
      <mu-flat-button class="noprint" label="Fermer" primary @click="close"/>
    </span>
  </mu-dialog>
</template>

<script>
import moment from 'moment'
import printJS from 'print-js'
// import 'print-js/dist/print.min.css'

export default {
  name: 'voucher-vol',
  props: ['open', 'transaction'],

  data () {
    return {
      passengerId: 0,
      background: false
    }
  },

  watch: {
    open (value) {
      if (value) {
        this.passengerId = 0
      }
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    print () {
      const voucherVol = document.getElementById('voucher-vol')
      voucherVol.style.paddingLeft = '4.5cm'
      voucherVol.style.paddingTop = '5.5cm'
      if (this.background) {
        voucherVol.classList.add('with-background')
      }

      printJS({
        printable: 'voucher-vol',
        type: 'html',
        font: 'Arial',
        honorColor: true,
        targetStyles: ['*'],
        style: `
          @page {
            size: A4;
            margin: 0;
            padding: 0;
          }

          #voucher-vol {
            min-height: 287mm !important;
          }
        `
      })

      voucherVol.style.paddingLeft = '0'
      voucherVol.style.paddingTop = '0'
      voucherVol.classList.remove('with-background')
    },

    flightDuration ({ departure, arrival }) {
      const departureDate = moment(departure, 'X')
      const arrivalDate = moment(arrival, 'X')

      const diff = Math.abs(departureDate.diff(arrivalDate, 'minutes'))
      return `${String(diff / 60).padStart(2, 0)}h:${String(diff % 60).padStart(2, 0)}m`
    }
  }
}
</script>

<style lang="scss">
  #voucher-vol {
    &.with-background {
      background: url('../../assets/voucher_vol.png') no-repeat top left;
      background-size: 100%;
    }

    margin: 1rem;
    padding: 2rem;
    color: #000;

    .panel-block {
      align-items: baseline;
    }

    table {
      border: 1px solid black;
      width: 100%;

      td, th {
        border: 1px solid black;
        padding: .5rem;
        text-align: center;
        vertical-align: middle;
      }
    }

    .itinerary {
      padding: .5rem 0;
    }
  }
</style>



