<template>
  <mu-dialog :open="open" @close="close" title="Transactions">
    <!-- Dialog body -->
    <div style="padding-top: 1rem">
      <mu-paper class="paper-card">
        <!-- Transactions tabs -->
        <mu-tabs :value="activeTransaction" @change="handleTabChange">
          <mu-tab
            v-for="(transaction, index) in transactions"
            :key="index"
            :value="index" :title="`Transactions #${index + 1}`"
          />
        </mu-tabs>

        <!-- Tabs content -->
        <div
          v-for="(transaction, index) in transactions"
          :key="index"
          v-if="activeTransaction === index"
          style="padding: 1rem"
        >
          <!-- Status -->
          <mu-select-field
            label="Status"
            v-model="transaction.status"
            style="vertical-align: bottom"
            labelFloat
          >
            <mu-menu-item
              v-for="(status, index) in statusTransactions"
              :key="index"
              :value="index"
              :title="status"
            />
          </mu-select-field>

          <!-- Stauts indicator -->
          <div style="display: inline-block; padding: 0 0 1rem 1rem">
            <!-- Status: -->

            <small
              :class="{ disabled: !transaction.printed, 'bolden': transaction.printed }"
            >Imprimé</small>

            <small
              :class="{ disabled: !transaction.delivered, 'bolden': transaction.printed }"
            >Livré</small>
          </div>

          <br/>

          <mu-select-field
            label="Type"
            v-model="transaction.type"
            style="vertical-align: bottom"
            labelFloat
          >
            <mu-menu-item
              v-for="(type, index) in ['livraison', 'locale', 'ccp']" :key="index"
              :title="type" :value="type"
            />
          </mu-select-field>

          <b-collapse class="panel" :open="true">
            <div slot="trigger" class="panel-heading">
              <strong>Prix</strong>
            </div>

            <div class="panel-block" style="display: block">
              <!-- Price field -->
              <mu-text-field
                type="number"
                label="Prix"
                v-model="transaction.price.amount"
                labelFloat
                :disabled="(type === 'vol' || type === 'visa') && parseInt(index, 10) === 0"
              />

              <!-- Commission field -->
              <mu-text-field
                v-if="(type === 'vol' || type === 'visa') && parseInt(index, 10) !== 0"
                type="number"
                label="Commission"
                v-model="transaction.price.commission"
                labelFloat
              />

              <br/>

              <!-- Price type field -->
              <mu-select-field
                label="Type transaction"
                v-model="transaction.price.type"
                style="vertical-align: bottom"
                labelFloat
              >
                <mu-menu-item :value="1" title="Paiement / Changement"/>
                <mu-menu-item :value="-1" title="Remboursement"/>
              </mu-select-field>

              <!-- Delivery price field -->
              <mu-text-field
                style="display: none"
                type="number"
                label="Prix livraison"
                v-model="transaction.price.deliveryPrice"
                labelFloat
              />
            </div>
          </b-collapse>

          <b-collapse class="panel">
            <div slot="trigger" class="panel-heading">
              <strong>Livraison</strong>
            </div>

            <div class="panel-block" style="display: block">
              <!-- Scheduled delivery dates -->
              <fr-date-picker
                label="Date de livraison"
                v-model="transaction.deliveryDate.scheduled"
              />

              <!-- Delivered date -->
              <fr-date-picker
                label="Date livréée"
                :disabled="true"
                v-model="transaction.deliveryDate.delivered"
              />

              <br/>

              <!-- Person name -->
              <mu-text-field
                v-model="transaction.address.name"
                label="Nom du destinataire"
                labelFloat
              />

              <!-- Scheduled delivery time of day -->
              <mu-select-field
                style="vertical-align: bottom"
                v-model="transaction.deliveryDate.timeOfDay"
                label="Heure"
                labelFloat
              >
                <mu-menu-item
                  :value="parseInt(0, 10)"
                  title="Matinée"
                />

                <mu-menu-item
                  :value="parseInt(1, 10)"
                  title="Après-midi"
                />
              </mu-select-field>

              <!-- Address -->
              <h2 style="padding-top: 1rem; margin-bottom: 0" class="subtitle">Adresse:</h2>
              <!-- Wilaya -->
              <mu-select-field
                v-model="transaction.address.wilaya"
                label="Wilaya"
                labelFloat
                style="vertical-align: bottom"
                :maxHeight="250"
              >
                <mu-menu-item
                  v-for="(w, index) in wilayasOrdered" :key="index"
                  :value="parseInt(w._id, 10)" :title="`${w.code} - ${w.name}`"
                />
              </mu-select-field>

              <!-- Communes -->
              <mu-select-field
                v-model="transaction.address.commune"
                label="Communes"
                labelFloat
                style="vertical-align: bottom"
                separator="; "
                :maxHeight="250"
              >
                <mu-menu-item
                  v-for="(commune, index) in communes(transaction.address.wilaya)" :key="index"
                  :value="parseInt(commune._id, 10)" :title="commune.name"
                />
              </mu-select-field>

              <br/>

              <!-- Address -->
              <mu-text-field
                v-model="transaction.address.address"
                label="Adresse"
                multiLine
                :rows="3"
                labelFloat
              />
            </div>
          </b-collapse>

          <div style="display: flex">
            <!-- Save button -->
            <div style="flex: 9">
              <mu-raised-button primary :disabled="disabled" label="Sauvegarder" @click="save(transaction, index)"/>
            </div>

            <!-- Remove button -->
            <div style="flex: 1">
              <mu-raised-button label="Supprimer" @click="promptRemove(transaction, index)" />
            </div>
          </div>
        </div>
      </mu-paper>

      <!-- Add transaction button -->
      <div style="margin: 0 0 1rem 0; text-align: right">
        <mu-raised-button label="Ajouter" icon="add" @click="newTransaction"/>
      </div>

      <!-- Footer -->
      <footer>
        <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
          <!-- Button annuler -->
          <mu-raised-button
            label="Fermer"
            @click.prevent="close"
          />
        </div>
      </footer>
    </div>
  </mu-dialog>
</template>

<script>
import { cloneDeep, findKey, find, orderBy, isEmpty } from 'lodash'
import FrDatePicker from '@/components/datepicker'

import Collapse from 'buefy/src/components/collapse'

import VOLS_QUERY from '@/graphql/queries/volsQuery.gql'
import HOTELS_QUERY from '@/graphql/queries/hotelsQuery.gql'
import VISAS_QUERY from '@/graphql/queries/visasQuery.gql'
import TELEXES_QUERY from '@/graphql/queries/telexesQuery.gql'
import ASSURANCES_QUERY from '@/graphql/queries/assurancesQuery.gql'
import ADD_TRANSACTION_MUTATION from '@/graphql/mutations/addTransaction.gql'
import EDIT_TRANSACTION_MUTATION from '@/graphql/mutations/editTransaction.gql'
import DELETE_TRANSACTION_MUTATION from '@/graphql/mutations/deleteTransaction.gql'

export default {
  props: ['open', 'dossier', 'type', 'variables', 'wilayas'],
  components: { FrDatePicker, 'BCollapse': Collapse },

  computed: {
    statusTransactions () { return this.$store.getters.statusTransactions },
    wilayasOrdered () {
      return orderBy(this.wilayas, 'code')
    }
  },

  data () {
    return {
      transactions: {},
      activeTransaction: 0,
      disabled: false
    }
  },

  watch: {
    open (value) {
      if (value && this.dossier.transactions.length) {
        this.transactions = cloneDeep(this.dossier.transactions)

        // Format date for timepicker
        for (let transactionIndex in this.transactions) {
          this.transactions[transactionIndex].price.deliveryPrice = 400
          for (let dateIndex in this.transactions[transactionIndex].deliveryDate) {
            if (this.transactions[transactionIndex].deliveryDate[dateIndex]) {
              this.transactions[transactionIndex].deliveryDate[dateIndex] =
                this.formatDateToISO(this.transactions[transactionIndex].deliveryDate[dateIndex])
            }
          }
        }
      } else {
        this.transactions = [{
          status: 'NEW',
          price: {
            type: 1
          },
          deliveryDate: {
            scheduled: null,
            delivered: null,
            timeOfDay: 0
          },
          address: {
            wilaya: null,
            commune: null,
            address: null
          },
          dossierInfo: {
            type: this.type,
            id: this.dossier._id
          }
        }]
      }
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    handleTabChange (value) {
      this.activeTransaction = parseInt(value, 10)
    },

    communes (wilaya) {
      if (!isEmpty(this.wilayas) && wilaya) {
        return orderBy(find(this.wilayas, { _id: wilaya }).communes, 'name')
      }
    },

    newTransaction () {
      this.transactions.push({
        status: 'NEW',
        price: {
          type: 1
        },
        deliveryDate: {},
        address: {},
        dossierInfo: {
          type: this.type,
          id: this.dossier._id
        }
      })
      this.activeTransaction = this.transactions.length - 1
    },

    promptRemove (transaction, index) {
      this.$deleteModal.prompt({
        title: 'Supprimer transaction',
        onConfirm: () => {
          this.remove(transaction, index)
        }
      })
    },

    remove (transaction, index) {
      this.removeTransaction(transaction, index)
    },

    async removeTransaction (transaction, index) {
      if (transaction._id) {
        try {
          await this.$apollo.mutate({
            mutation: DELETE_TRANSACTION_MUTATION,
            variables: {
              _id: transaction._id,
              dossierInfo: {
                type: this.type,
                id: this.dossier._id
              }
            },
            update: (store, { response }) => {
              let q
              let dossierType

              if (this.type === 'vol') {
                q = VOLS_QUERY
                dossierType = 'vols'
              }

              if (this.type === 'hotel') {
                q = HOTELS_QUERY
                dossierType = 'hotels'
              }

              if (this.type === 'visa') {
                q = VISAS_QUERY
                dossierType = 'visas'
              }

              let data = store.readQuery({
                query: q,
                variables: { ...this.variables }
              })

              // Get dossier index
              const indexDossier = findKey(data[dossierType].data, {
                _id: this.dossier._id
              })

              // Get updated contact index
              const indexTransactions = findKey(
                data[dossierType].data[indexDossier].transactions,
                { _id: transaction._id }
              )

              data[dossierType].data[indexDossier].transactions.splice(indexTransactions, 1)

              store.writeQuery({
                query: q,
                variables: { ...this.variables },
                data
              })

              this.transactions.splice(index, 1)
              if (this.activeTransaction > 0) --this.activeTransaction
              else this.activeTransaction = 0
            }
          })
        } catch (error) {
          this.disabled = false
          console.error(error)
        }
      } else {
        this.transactions.splice(index, 1)
        if (this.activeTransaction > 0) --this.activeTransaction
        else this.activeTransaction = 0
      }
    },

    async save (transaction, index) {
      if (transaction._id) {
        this.editTransaction(transaction, index)
      } else {
        this.addTransaction(transaction, index)
      }
    },

    async addTransaction (transaction, index) {
      this.disabled = true

      try {
        await this.$apollo.mutate({
          mutation: ADD_TRANSACTION_MUTATION,
          variables: {
            ...transaction,
            dossierInfo: {
              type: this.type,
              id: this.dossier._id
            }
          },
          update: (store, { data: { addTransaction } }) => {
            let q
            let dossierType

            if (this.type === 'vol') {
              q = VOLS_QUERY
              dossierType = 'vols'
            }

            if (this.type === 'hotel') {
              q = HOTELS_QUERY
              dossierType = 'hotels'
            }

            if (this.type === 'visa') {
              q = VISAS_QUERY
              dossierType = 'visas'
            }

            if (this.type === 'telex') {
              q = TELEXES_QUERY
              dossierType = 'telexes'
            }

            if (this.type === 'assurance') {
              q = ASSURANCES_QUERY
              dossierType = 'assurances'
            }

            let data = store.readQuery({
              query: q,
              variables: { ...this.variables }
            })

            // Get dossier index
            const indexDossier = findKey(data[dossierType].data, {
              _id: this.dossier._id
            })

            data[dossierType].data[indexDossier].transactions.push(addTransaction)

            store.writeQuery({
              query: q,
              variables: { ...this.variables },
              data
            })
          }
        })
        this.disabled = false
      } catch (error) {
        this.disabled = false
        console.error(error)
      }
    },

    async editTransaction (transaction, index) {
      this.disabled = true

      try {
        await this.$apollo.mutate({
          mutation: EDIT_TRANSACTION_MUTATION,
          variables: {
            ...transaction
          },
          update: (store, { data: { editTransaction } }) => {
            let q
            let dossierType

            if (this.type === 'vol') {
              q = VOLS_QUERY
              dossierType = 'vols'
            }

            if (this.type === 'hotel') {
              q = HOTELS_QUERY
              dossierType = 'hotels'
            }

            if (this.type === 'visa') {
              q = VISAS_QUERY
              dossierType = 'visas'
            }

            if (this.type === 'telex') {
              q = TELEXES_QUERY
              dossierType = 'telexes'
            }

            if (this.type === 'assurance') {
              q = ASSURANCES_QUERY
              dossierType = 'assurances'
            }

            let data = store.readQuery({
              query: q,
              variables: { ...this.variables }
            })

            // Get dossier index
            const indexDossier = findKey(data[dossierType].data, {
              _id: this.dossier._id
            })

            // Get updated contact index
            const indexTransactions =
              findKey(data[dossierType].data[indexDossier].transactions, { _id: editTransaction._id })

            data[dossierType].data[indexDossier].transactions[indexTransactions] = editTransaction

            store.writeQuery({
              query: q,
              variables: { ...this.variables },
              data
            })

            this.transactions[index] = editTransaction
            this.disabled = false
          }
        })
      } catch (error) {
        this.disabled = false
        console.error(error)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .disabled {
    text-decoration: line-through;
    color: #dddddd;
  }

  .bolden {
    font-weight: bold;
  }
</style>



