import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import Vols from '@/views/Vols'
import Hotels from '@/views/Hotels'
import Visas from '@/views/Visas'
import Telex from '@/views/Telex'
import Assurances from '@/views/Assurances'
import Transactions from '@/views/Transactions'
import Users from '@/views/Users'
import Roles from '@/views/Roles'
import Commissions from '@/views/Commissions'
import VisasPrices from '@/views/VisasPrices'
import Deposits from '@/views/Deposits'
import Login from '@/views/Login'
import DeliveryZones from '@/views/DeliveryZones'
import Wilayas from '@/views/Wilayas'
import Destinations from '@/views/Destinations'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        title: 'Accueil'
      }
    },

    {
      path: '/dossiers/vols',
      name: 'Vols',
      component: Vols,
      meta: {
        title: 'Dossiers : Vols'
      }
    },

    {
      path: '/dossiers/hotels',
      name: 'Hotels',
      component: Hotels,
      meta: {
        title: 'Dossiers : Hotels'
      }
    },

    {
      path: '/dossiers/visas',
      name: 'Visas',
      component: Visas,
      meta: {
        title: 'Dossiers : Visas'
      }
    },

    {
      path: '/dossiers/telex',
      name: 'Telex',
      component: Telex,
      meta: {
        title: 'Dossiers : Telex'
      }
    },

    {
      path: '/dossiers/assurances',
      name: 'Assurances',
      component: Assurances,
      meta: {
        title: 'Dossiers : Assurances'
      }
    },

    {
      path: '/dossiers/transactions',
      name: 'Transactions',
      component: Transactions,
      meta: {
        title: 'Dossiers : Livraisons'
      }
    },

    {
      path: '/settings/commissions',
      name: 'Commissions',
      component: Commissions,
      meta: {
        title: 'Commissions'
      }
    },

    {
      path: '/settings/visas-prices',
      name: 'VisasPrices',
      component: VisasPrices,
      meta: {
        title: 'Prix visas'
      }
    },

    {
      path: '/settings/visas-deposits',
      name: 'VisasDeposits',
      component: Deposits,
      meta: {
        title: 'Deposits visas'
      }
    },

    {
      path: '/settings/users',
      name: 'Users',
      component: Users,
      meta: {
        title: 'Utilisateurs'
      }
    },

    {
      path: '/settings/roles',
      name: 'Roles',
      component: Roles,
      meta: {
        title: 'Roles'
      }
    },

    {
      path: '/site/destinations',
      name: 'Destinations',
      component: Destinations,
      meta: {
        title: 'Destinations'
      }
    },

    {
      path: '/delivery/zones',
      name: 'DeliveryZones',
      component: DeliveryZones,
      meta: {
        title: 'Zones livraisons'
      }
    },

    {
      path: '/delivery/wilayas',
      name: 'Wilayas',
      component: Wilayas,
      meta: {
        title: 'Wilayas'
      }
    },

    {
      path: '/login',
      name: 'Login',
      component: Login
    }
  ]
})

export default router



