<template>
  <mu-dialog :open="open" @close="close" title="Deposit visa">
    <div>
      <mu-text-field
        label="Nom"
        v-model="deposit.name"
      />
    </div>

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
        />

        <!-- Button sauvegarder -->
        <mu-raised-button
          label="Sauvegarder"
          @click.prevent="save"
          style="margin: 0 1rem"
          primary
        />
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
import { cloneDeep } from 'lodash'

export default {
  name: 'edit-visa-deposit',
  props: ['open', 'visaDeposit'],

  data () {
    return {
      deposit: {}
    }
  },

  watch: {
    visaDeposit (value) {
      this.deposit = cloneDeep(value)
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    async save () {
      this.$emit('update', this.deposit)
      this.close()
    }
  }
}
</script>



