<template>
  <div class="content-wrapper">
    <!-- New user button -->
    <div style="text-align: right">
      <mu-flat-button label="Ajouter role" @click.prevent="newRole()" />
    </div>

    <b-table :data="roles" striped :loading="$apollo.queries.roles.loading">
      <template slot-scope="props">
        <!-- Nom -->
        <b-table-column field="name" label="Nom">
          <a @click.prevent="editRole(props.row)">{{ props.row.name }}</a>
        </b-table-column>

        <b-table-column label="Ressources">
          <div v-for="(permissions, resource) in props.row.permissions" :key="resource">
            {{ resource }}:
            <span class="role-span" v-for="(permission, index) in permissions" :key="index">
              {{ permission }}
            </span>
          </div>
        </b-table-column>

        <!-- Options -->
        <b-table-column label="Options">
          <!-- Edit -->
          <b-tooltip label="Editer" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="editRole(props.row)"
            >
              <mu-icon value="edit" :size="18"/>
            </a>
          </b-tooltip>
          <b-tooltip label="Supprimer" position="is-top">
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="promptRemoveRole(props.row)"
            >
              <mu-icon value="delete" :size="18"/>
            </a>
          </b-tooltip>
        </b-table-column>
      </template>
    </b-table>

    <edit-role :open.sync="roleModal" :data="role" :resources="resources" />
  </div>
</template>

<script>
import gql from 'graphql-tag'
import { findKey } from 'lodash'
import EditRole from '@/components/roles/EditRole'
import ROLES_QUERY from '@/graphql/queries/rolesQuery.gql'
import DELETE_ROLE_MUTATION from '@/graphql/mutations/deleteRole.gql'

export default {
  name: 'roles',
  components: { EditRole },

  data () {
    return {
      roles: [],
      resources: [],
      role: {},
      roleModal: false
    }
  },

  apollo: {
    roles: {
      query: ROLES_QUERY,
      fetchPolicy: 'network-only'
    },

    resources: {
      query: gql`{ resources }`,
      fetchPolicy: 'network-only'
    }
  },

  methods: {
    editRole (role) {
      this.role = role
      this.roleModal = true
    },

    newRole () {
      this.$dialog.prompt({
        title: 'Nom :',
        onConfirm: (name) => {
          this.editRole({ name, permissions: {} })
        }
      })
    },

    promptRemoveRole (role) {
      this.$deleteModal.prompt({
        title: 'Supprimer utilisateur',
        onConfirm: () => {
          this.removeRole(role.name)
        }
      })
    },

    async removeRole (role) {
      try {
        await this.$apollo.mutate({
          mutation: DELETE_ROLE_MUTATION,
          variables: { role },
          update (store, { response }) {
            let data = store.readQuery({ query: ROLES_QUERY })
            // Get user index
            const indexUser = findKey(data.roles, { name: role })

            data.roles.splice(indexUser, 1)

            store.writeQuery({
              query: ROLES_QUERY,
              data
            })
          }
        })
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>

<style lang="scss">
  .role-span {
    &::after {
      content: ' - '
    }

    &:last-child::after {
      content: ''
    }
  }
</style>



