<template>
  <mu-dialog :open="open" @close="close" title="Prix visa">
    <div>
      <mu-text-field
        label="Code"
        v-model="price.code"
      />

      <mu-text-field
        label="Nom"
        v-model="price.displayName"
      />

      <br/>

      <mu-text-field
        label="Prix"
        type="number"
        v-model="price.price"
      />

      <mu-text-field
        label="Commission"
        type="number"
        v-model="price.commission"
      />

      <br />

      <mu-select-field
        v-model="price.deposit"
        label="Deposit"
        labelFloat
        style="vertical-align: bottom"
        :maxHeight="250"
      >
        <mu-menu-item
          v-for="(deposit, index) in deposits" :key="index"
          :value="deposit._id" :title="deposit.name"
        />
      </mu-select-field>
    </div>

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
        />

        <!-- Button sauvegarder -->
        <mu-raised-button
          label="Sauvegarder"
          @click.prevent="save"
          style="margin: 0 1rem"
          primary
        />
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
import { cloneDeep } from 'lodash'

export default {
  name: 'edit-visa-price',
  props: ['open', 'visaPrice', 'deposits'],

  data () {
    return {
      price: {}
    }
  },

  watch: {
    visaPrice (value) {
      this.price = cloneDeep(value)
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    async save () {
      this.$emit('update', this.price)
      this.close()
    }
  }
}
</script>



