<template>
  <div id="app">
    <main>
      <default-app-bar @toggleNav="toggleNav" :open="open"/>

      <!-- App navigation drawer -->
      <app-nav
        v-if="$route.name !== 'Login'"
        @change="handleMenuChange"
        :open="open"
        :docked="docked"
        @close="toggleNav">
      </app-nav>

      <!-- App content -->
      <div
        id="content" :class="{'nav-hide': !open || $route.name === 'Login'}"
        :style="$route.name === 'Login' ? 'padding: 0' : ''"
      >
        <router-view></router-view>
      </div>
    </main>
  </div>
</template>

<script>
import AppNavDrawer from '@/components/home/AppNavDrawer'
import DefaultAppBar from '@/components/home/DefaultAppBar'
import { mapActions, mapGetters } from 'vuex'
import ME_QUERY from '@/graphql/queries/me.gql'

export default {
  name: 'app',
  components: { 'app-nav': AppNavDrawer, DefaultAppBar },

  data () {
    const desktop = isDesktop()

    return {
      open: desktop,
      docked: desktop,
      desktop: desktop
    }
  },

  computed: {
    ...mapGetters(['user', 'isLoggedIn'])
  },

  watch: {
    isLoggedIn (value) {
      if (value) {
        if (this.$route.name === 'Login') {
          this.$router.push({ name: 'Home' })
        }
      } else {
        if (this.$route.name !== 'Login') {
          this.$router.push({ name: 'Login' })
        }
      }
    }
  },

  methods: {
    ...mapActions(['setPageTitle', 'setUser', 'setLoggedIn']),

    toggleNav () {
      this.open = !this.open
    },

    changeNav () {
      const desktop = isDesktop()
      this.docked = desktop
      if (desktop === this.desktop) return

      if (!desktop && this.desktop && this.open) {
        this.open = false
      }
      if (desktop && !this.desktop && !this.open) {
        this.open = true
      }

      this.desktop = desktop
    },

    handleMenuChange () {
      if (!this.desktop) this.open = false
      this.setPageTitle({ title: this.$route.meta.title })
    },

    async getUser () {
      try {
        const { data: { me } } = await this.$apollo.query({
          query: ME_QUERY,
          fetchPolicy: 'network-only'
        })

        this.setUser({ user: me })
        this.setLoggedIn({ status: true })

        // Update user abilities
        const permissions = []
        Object.keys(me.permissions).forEach(resource => {
          permissions.push({ subject: resource, actions: me.permissions[resource] })
        })
        this.$ability.update(permissions)
      } catch (error) {
        if (this.$route.name !== 'Login') {
          this.$router.push({ name: 'Login' })
        }
      }
    }
  },

  async mounted () {
    this.setPageTitle({ title: this.$route.meta.title })
    this.changeNav()

    this.handleResize = () => {
      this.changeNav()
    }
    window.addEventListener('resize', this.handleResize)

    await this.getUser()
  },

  destroyed () {
    window.removeEventListener('resize', this.handleResize)
  }
}

function isDesktop () {
  return window.innerWidth > 993
}
</script>

<style lang="scss">
  .noprint {
    @media print {
      display: none;
    }
  }

  html {
    overflow: hidden;
  }

  .modal {
    z-index: 20141250;
  }

  #content {
    left: 256px;
  }

  .app-bar, #content {
    position: fixed;
    // left: 256px;
    transition: all .45s cubic-bezier(.23,1,.32,1);

    .right {
      // padding-right: 256px;
    }

    &.nav-hide {
      left: 0;

      .right {
        padding-right: 0;
      }
    }
  }

  #content {
    padding-top: 64px;
    width: calc(100% - 256px);
    height: 100vh;
    overflow-y: auto;
    overflow-x: hidden;

    &.nav-hide {
      width: 100%;
    }

    @media (max-width: 480px) {
      padding-top: 56px;
    }
  }

  .content-wrapper {
    padding: 1rem 2rem;
    width: 100%;
    overflow-x: scroll;
  }

  //

  table {
    a {
      font-weight: 500;
      color: #363636;
    }
  }

  .mu-dialog-wrapper {
    overflow-y: scroll;
  }

  .mu-dialog {
    max-width: initial;
    width: auto;
    max-height: 95%;
    max-width: 95%;
    overflow-y: scroll;

    .mu-dialog-body {
      padding-top: 0;
      padding-bottom: 0;
    }

    .mu-dialog-title {
      border-bottom: 1px solid #f4f4f4;
      line-height: 1rem;
    }
  }

  // Dialog styles
  .mu-dialog {
    .title-bar {
      height: 50px;
      background-color: #e8e8e8;

      h2 {
        line-height: 50px;
        padding: 0 1rem;
        color: #444;
      }

      .mu-badge-container {
        display: inline-block;
        vertical-align: text-bottom
      }
    }

    footer {
      line-height: 1rem;
      color: rgba(0, 0, 0, 0.87);
      padding: 24px 24px 20px;
      margin: 0 -24px;
      padding: 0 1rem;
      border-top: 1px solid #f4f4f4;
    }

    .paper-card {
      margin: 0 0 2rem 0;
    }
  }

  .tabs .is-disabled {
    pointer-events: none;
    opacity: .65;
  }
</style>



