<template>
  <mu-dialog title="Délai d'emission" :open="open" @close="close">
    <fr-date-picker
      container="inline"
      label="Date"
      v-model="date"
      style="margin-right: 1rem"
    ></fr-date-picker>

    <mu-time-picker
      container="inline"
      format="24hr"
      label="Heure"
      okLabel="Ok"
      cancelLabel="Fermer"
      labelFloat
      v-model="time"
    ></mu-time-picker>

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          secondary label="Fermer"
          @click.prevent="close"
        ></mu-raised-button>

        <!-- Button valider -->
        <mu-raised-button
          primary
          label="Valider"
          style="margin: 0 .5rem"
          @click.prevent="update"
        ></mu-raised-button>
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
import moment from 'moment'
import FrDatePicker from '@/components/datepicker'

export default {
  name: 'emission-limit',
  props: ['open', 'dossier'],
  components: { FrDatePicker },

  data () {
    return {
      date: null,
      time: null
    }
  },

  watch: {
    dossier (value) {
      const dateTime = moment(value.emissionLimit, 'X')
      this.date = dateTime.clone().format('YYYY-MM-DD')
      this.time = dateTime.clone().format('HH:mm')
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    update () {
      this.$emit('update', {
        pnr: this.dossier.pnr.original,
        emissionLimit: `${this.time}  ${this.date}`
      })
    }
  }
}
</script>



