<template>
  <mu-dialog :open="open" @close="close" id="voucher-visa-container" v-if="Object.keys(transaction).length && transaction.visa">
    <div style="padding: 1rem">
      <mu-switch v-model="background" label="Image de fond"></mu-switch>
    </div>

    <div id="voucher-visa">
      <!-- Dossier details -->
      <div id="dossier-details">
        <div style="flex: 1">
          <table>
            <tr>
              <td>
                <p><b>Nom et prénom:</b></p>
                {{ transaction.visa.name }}
                </td>
            </tr>

            <tr>
              <td>
                <p><b>Type de passport :</b></p>
                Passport oridnaire
              </td>
            </tr>

            <tr>
              <td>
                <p><b>N&deg; tel. :</b></p>
                {{ $get(transaction, 'visa.contacts[0].phones[0].number', '-') }}
                </td>
            </tr>

            <tr>
              <td>
                <p><b>Numéro de passport :</b></p>
                {{ transaction.visa.passportNumber || '-' }}
              </td>
            </tr>
          </table>
        </div>

        <!-- <div class="spacer"></div>

        <div style="flex: 1">
          <table>
            <tr>
              <td>
                <p><b>N&deg; tel. :</b></p>
                {{ transaction.visa.contacts[0].phones[0].number }}
                </td>
            </tr>

            <tr>
              <td>
                <p><b>Numéro de passport :</b></p>
                {{ transaction.visa.passportNumber || '-' }}
              </td>
            </tr>
          </table>
        </div> -->
      </div>

      <!-- Request details -->
      <div id="request-details">
        <div style="flex: 1">
          <table>
            <tr>
              <td>
                <p><b>Pays :</b></p>
                {{ transaction.visa.destination || '-' }}
              </td>
            </tr>

            <tr>
              <td>
                <p><b>Type de visa demandé :</b></p>
                Touristique
              </td>
            </tr>

            <tr>
              <td>
                <p><b>Date de départ :</b></p>
                {{ formatDate(transaction.visa.departureDate) }}
              </td>
            </tr>

            <tr>
              <td>
                <p><b>Validité du visa :</b></p>
                {{ transaction.visa.duration || '-' }}
              </td>
            </tr>
          </table>
        </div>
        <div class="spacer"></div>
        <img
          class="passport-image"
          v-if="transaction.visa.passport"
          :src="`/api/static${transaction.visa.passport}`"
        />
      </div>
    </div>

    <!-- Action buttons -->
    <span slot="title">Impression voucher visa</span>
    <span slot="title">
      <mu-flat-button class="noprint" label="Imprimer" secondary @click="print()"/>
      <mu-flat-button class="noprint" label="Fermer" primary @click="close"/>
    </span>
  </mu-dialog>
</template>

<script>
import printJS from 'print-js'

export default {
  name: 'voucher-visa',
  props: ['open', 'transaction'],

  data () {
    return {
      personId: 0,
      background: false
    }
  },

  watch: {
    open (value) {
      if (value) {
        this.personId = 0
      }
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    print () {
      const voucherVisa = document.getElementById('voucher-visa')
      if (this.background) {
        voucherVisa.classList.add('with-background')
      }

      printJS({
        printable: 'voucher-visa',
        type: 'html',
        font: 'Arial',
        honorColor: true,
        targetStyles: ['*'],
        style: `
          @page {
            size: A4;
            margin: 0;
            padding: 0;
          }

          #voucher-visa {
            min-height: 297mm !important;
            min-width: 210mm !important;
          }

          #dossier-details {
            padding-top: 4.8cm !important;
            padding-left: .7cm !important;
            width: 100% !important;
          }

          #request-details {
            position: absolute !important;
            padding: 0 !important;
            top: 165mm !important;
            left: 1.7cm !important;
          }
        `
      })

      voucherVisa.classList.remove('with-background')
    }
  }
}
</script>

<style lang="scss">
  #voucher-visa {
    &.with-background {
      background: url('../../assets/voucher_visa.png') no-repeat top left;
      background-size: 100%;
    }

    margin: 1rem;
    padding: 1rem;
    color: #000;

    #dossier-details {
      display: flex;
      min-width: 180mm;
    }

    #request-details {
      display: flex;
      padding-top: 2rem;
    }

    .passport-image {
      max-width: 8cm;
      max-height: 6.5cm;
    }

    .spacer {
      width: .5rem;
    }

    table {
      border: 1px solid black;
      width: 100%;

      td, th {
        border: 1px solid black;
        padding: 0 .5rem;
        vertical-align: middle;
      }
    }
  }
</style>



