<template>
  <mu-dialog :open="open" @close="close" id="voucher-hotel-container" v-if="Object.keys(transaction).length && transaction.hotel">
    <!-- Tabs -->
    <b-tabs
      v-if="transaction.hotel.paxes.length > 1"
      style="padding-top: 1rem"
      class="noprint"
      v-model="personId"
      type="is-boxed"
      position="is-centered"
    >
      <b-tab-item
        v-for="(person, index) in transaction.hotel.paxes"
        :key="index"
        :label="`Guest #${index + 1}`"
      ></b-tab-item>
    </b-tabs>

    <div style="padding: 1rem">
      <mu-switch v-model="background" label="Image de fond"></mu-switch>
    </div>

    <div id="voucher-hotel">
      <!-- Guest / User info -->
      <table>
        <thead>
          <tr>
            <th>Nom</th>
            <th>N° Tel</th>
            <th>N° Res</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <!-- Nom -->
            <td>
              {{ $get(transaction, `hotel.paxes[${personId}].lastName`, '-') }}
              {{ $get(transaction, `hotel.paxes[${personId}].firstName`, '-') }}
            </td>

            <!-- Telephone -->
            <td>
              <span v-if="transaction.hotel.contacts.length">
                {{ $get(transaction, 'hotel.contacts[0].phones[0].number', '-') }}
              </span>
            </td>

            <!-- Booking ref. -->
            <td> {{ transaction.hotel.bookingRef || transaction.hotel.secondaryBookingRef }} </td>
          </tr>
        </tbody>
      </table>

      <!-- Details -->
      <table id="details" style="margin-top: 1.65cm">
        <tbody>
          <tr>
            <td style="max-width: 4.5cm">{{ transaction.hotel.hotelName }}</td>
            <td>
              {{ transaction.hotel.city }}<br/>
              {{ transaction.hotel.country }}
            </td>
            <td style="padding: 0">
              <table style="margin: -1px">
                <thead>
                  <tr>
                    <th>Check-in</th>
                    <th>Check-out</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>{{ formatDate(transaction.hotel.depDate) }}</td>
                    <td>{{ formatDate(transaction.hotel.arrDate) }}</td>
                  </tr>
                </tbody>
              </table>
            </td>
          </tr>

          <tr>
            <td><b>Coordonées</b></td>
            <td colspan="3" style="text-align: left">
              <span v-html="transaction.hotel.hotelAddress || '-'"></span><br/>
              {{ transaction.hotel.hotelPhone || '-' }}<br/>
            </td>
          </tr>
        </tbody>
      </table>

      <!-- Guests -->
      <h2 class="title" style="padding-top: 1.5cm">Guest{{ transaction.hotel.paxes.length > 1 ? 's' : '' }}:</h2>
      <ul style="list-style-type: circle; padding-left: 2rem; padding-bottom: 2rem">
        <li v-for="(person, index) in transaction.hotel.paxes" :key="index">
          <span class="is-capitalized">{{ person.lastName }} {{ person.firstName }}</span>
        </li>
      </ul>

      <!-- Chambres / Rooms -->
      <table v-for="(room, index) in transaction.hotel.rooms" :key="index">
        <tbody>
          <tr>
            <td>Chambre #{{ index + 1 }}</td>
            <td>
              {{ room.room }}
            </td>
            <td>
              <b>Pension:</b><br/>
              {{ room.board }}
            </td>
            <td style="padding: 0">
              <table style="margin: -1px">
                <thead>
                  <tr>
                    <th>Adultes</th>
                    <th>Enfants</th>
                    <th>Bébés</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>{{ room.paxes[0].adults }}</td>
                    <td>{{ room.paxes[0].infant }}</td>
                    <td>{{ room.paxes[0].child }}</td>
                  </tr>
                </tbody>
              </table>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Action buttons -->
    <span slot="title">Impression voucher hotel</span>
    <span slot="title">
      <mu-flat-button class="noprint" label="Imprimer" secondary @click="print()"/>
      <mu-flat-button class="noprint" label="Fermer" primary @click="close"/>
    </span>
  </mu-dialog>
</template>

<script>
import printJS from 'print-js'

export default {
  name: 'voucher-hotel',
  props: ['open', 'transaction'],

  data () {
    return {
      personId: 0,
      background: false
    }
  },

  watch: {
    open (value) {
      if (value) {
        this.personId = 0
      }
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    print () {
      const voucherHotel = document.getElementById('voucher-hotel')
      voucherHotel.style.paddingLeft = '4.8cm'
      voucherHotel.style.paddingTop = '5.5cm'
      if (this.background) {
        voucherHotel.classList.add('with-background')
      }

      printJS({
        printable: 'voucher-hotel',
        type: 'html',
        font: 'Arial',
        honorColor: true,
        targetStyles: ['*'],
        style: `
          @page {
            size: A4;
            margin: 0;
            padding: 0;
          }

          #voucher-hotel {
            min-height: 287mm !important;
          }
        `
      })

      voucherHotel.style.paddingLeft = '0'
      voucherHotel.style.paddingTop = '0'
      voucherHotel.classList.remove('with-background')
    }
  }
}
</script>

<style lang="scss">
  #voucher-hotel {
    &.with-background {
      background: url('../../assets/voucher_hotel.png') no-repeat top left;
      background-size: 100%;
    }

    margin: 1rem;
    padding: 1rem;
    color: #000;

    table {
      border: 1px solid black;
      width: 100%;

      td, th {
        border: 1px solid black;
        padding: .5rem;
        text-align: center;
        vertical-align: middle;
      }
    }
  }
</style>



