<template>
  <mu-dialog :open="open" @close="close" title="Editer demande visa">
    <!-- Client name -->
    <mu-text-field
      v-model="newDossier.name"
      label="Nom"
    />

    <!-- Client passport number -->
    <mu-text-field
      v-model="newDossier.passportNumber"
      label="Numéro de passport"
    />

    <br />

    <!-- Client visa number of entries -->
    <mu-text-field
      v-model="newDossier.entries"
      label="Nombre d'entrées"
      type="number"
    />

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
        />

        <!-- Button sauvegarder -->
        <mu-raised-button
          label="Sauvegarder"
          @click.prevent="save"
          style="margin: 0 1rem"
          primary
        />
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
export default {
  name: 'edit-visa',
  props: ['open', 'dossier'],

  data () {
    return {
      newDossier: {}
    }
  },

  watch: {
    dossier (value) {
      this.newDossier = {
        _id: value._id,
        name: value.name,
        entries: value.entries,
        passportNumber: value.passportNumber
      }
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    save () {
      this.$emit('update', this.newDossier)
    }
  }
}
</script>



