<template>
  <!-- App bar -->
    <mu-appbar
      v-if="$route.name !== 'Login'"
      :title="`Votel v2 - ${title ? title : ''}`"
      class="app-bar"
      :class="{'nav-hide': !open}"
      :zDepth="0"
    >
      <mu-icon-button icon="menu" slot="left" @click="toggleNav"/>
      <!-- User menu -->
      <a @click="toggleMenu" slot="right" class="has-text-white">{{ $user.name }}</a>
      <mu-icon-menu
        :open="showMenu"
        icon="more_vert"
        slot="right"
        :anchorOrigin="leftTop"
        :targetOrigin="leftBottom"
      >
        <mu-menu-item title="Déconnexion" @click="logout"/>
      </mu-icon-menu>
    </mu-appbar>
</template>

<script>
import { mapActions } from 'vuex'
import LOGOUT_QUERY from '@/graphql/queries/logout.gql'

export default {
  name: 'default-app-bar',
  props: ['open'],

  data () {
    return {
      showMenu: false,

      leftTop: {horizontal: 'left', vertical: 'top'},
      leftBottom: {horizontal: 'left', vertical: 'bottom'},
      rightBottom: {horizontal: 'right', vertical: 'bottom'},
      rightTop: {horizontal: 'right', vertical: 'top'}
    }
  },

  computed: {
    title () { return this.$store.getters.pageTitle }
  },

  methods: {
    ...mapActions(['setUser', 'setLoggedOut']),
    toggleNav () {
      this.$emit('toggleNav')
    },

    toggleMenu () {
      this.showMenu = !this.showMenu
    },

    async logout () {
      try {
        await this.$apollo.query({
          query: LOGOUT_QUERY
        })

        this.setLoggedOut()
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>



