<template>
  <mu-dialog
    :title="`Dossier: #${dossier._id} - ${pnr}`"
    :open="open"
    @close="close"
  >
    <!-- Body content -->
    <div style="padding-top: 1rem" v-if="Object.keys(this.dossier).length">
      <!-- Réservations -->
      <mu-paper class="paper-card">
        <div class="title-bar">
          <h2>
            Réservations
            <mu-badge :content="String(dossier.passengers.length)"></mu-badge>
          </h2>
        </div>

        <b-table :data="dossier.passengers" striped narrowed>
          <template slot-scope="props">
            <!-- Nom passager -->
            <b-table-column label="Nom">
              {{ props.row.lastName }}
            </b-table-column>

            <!-- Prenom Passager -->
            <b-table-column label="Prénom">
              {{ props.row.name }}
            </b-table-column>

            <!-- ETicket -->
            <b-table-column label="E-Ticket">
              {{ props.row.eTicket || '-' }}
            </b-table-column>

            <!-- Delai d'emission -->
            <b-table-column label="Delai d'emission">
              {{ formatDate(props.row.emissionLimit) }}
            </b-table-column>

            <!-- Date d'emission -->
            <b-table-column label="Date d'emission">
              {{ formatDate(props.row.emissionDate) }}
            </b-table-column>
          </template>
        </b-table>
      </mu-paper>

      <!-- Paiements -->
      <mu-paper class="paper-card">
        <div class="title-bar">
          <h2>
            Paiements
            <mu-badge :content="String(dossier.payments.length)"></mu-badge>
          </h2>
        </div>

        <b-table :data="dossier.payments" striped narrowed>
          <template slot-scope="props">
            <!-- Devise -->
            <b-table-column label="Devise">
              {{ props.row.currency }}
            </b-table-column>

            <!-- Prix HT -->
            <b-table-column label="Prix HT">
              {{ formatMoney(props.row.price.baseFare) }}
            </b-table-column>

            <!-- Prix Total unitaire -->
            <b-table-column label="Prix total/U">
              {{ formatMoney(props.row.price.total) }}
            </b-table-column>

            <!-- Prix total -->
            <b-table-column label="Prix total">
              {{ formatMoney(props.row.price.total * dossier.passengers.length) }}
            </b-table-column>

            <!-- Tax 1 -->
            <b-table-column label="Tax 1">{{ formatMoney(props.row.tax1.tax) }}</b-table-column>
            <b-table-column label="Code T1">{{ props.row.tax1.code }}</b-table-column>

            <!-- Tax 2 -->
            <b-table-column label="Tax 2">{{ formatMoney(props.row.tax2.tax) }}</b-table-column>
            <b-table-column label="Code T2">{{ props.row.tax2.code }}</b-table-column>

            <!-- Tax 3 -->
            <b-table-column label="Tax 3">{{ formatMoney(props.row.tax3.tax) }}</b-table-column>
            <b-table-column label="Code T3">{{ props.row.tax3.code }}</b-table-column>
          </template>
        </b-table>
      </mu-paper>

      <!-- Itinéraires -->
      <mu-paper class="paper-card">
        <div class="title-bar">
          <h2>
            Itinéraires
            <mu-badge :content="String(dossier.itineraries.length)"></mu-badge>
          </h2>
        </div>

        <b-table :data="dossier.itineraries" striped narrowed>
          <template slot-scope="props">
            <!-- Compagnie aérienne -->
            <b-table-column label="Compagnie Aérienne">
              [{{ props.row.airline.code }}]
              {{ props.row.airline.name }}
            </b-table-column>

            <!-- Code compagnie aérienne -->
            <b-table-column label="N° compagnie">
              {{ props.row.airline.number }}
            </b-table-column>

            <!-- Numéro de vol -->
            <b-table-column label="N° de vol">
              {{ props.row.flight.number }}
            </b-table-column>

            <!-- Date départ -->
            <b-table-column label="Date départ">
              {{ formatDateTime(props.row.departure.date) }}
            </b-table-column>

            <!-- Ville départ -->
            <b-table-column label="Ville départ">
              [{{ props.row.departure.airport.code }}]
              {{ props.row.departure.airport.city }} /
              {{ props.row.departure.airport.name }}
            </b-table-column>

            <!-- Date arrivée -->
            <b-table-column label="Date arrivée">
              {{ formatDateTime(props.row.arrival.date) }}
            </b-table-column>

            <!-- Ville arrivée -->
            <b-table-column label="Ville arrivée">
              [{{ props.row.arrival.airport.code }}]
              {{ props.row.arrival.airport.city }} /
              {{ props.row.arrival.airport.name }}
            </b-table-column>

            <!-- Bagage -->
            <b-table-column label="Bagage">
              {{ props.row.baggage }}
            </b-table-column>

            <!-- Type d'avion -->
            <b-table-column label="Type avion">
              {{ props.row.aircraft }}
            </b-table-column>

            <!-- Durée vol -->
            <b-table-column label="Durée">
              {{ props.row.duration }}
              <!-- {{ flightDuration(props.row) }} -->
            </b-table-column>
          </template>
        </b-table>
      </mu-paper>

      <!-- Contacts -->
      <mu-paper class="paper-card">
        <div class="title-bar">
          <h2>
            Contacts
            <mu-badge :content="String(dossier.contacts.length)"></mu-badge>
          </h2>
        </div>

        <b-table :data="dossier.contacts" striped narrowed>
          <template slot-scope="props">
            <!-- Nom -->
            <b-table-column label="Nom">
              {{ props.row.name }}
            </b-table-column>

            <!-- Telephone -->
            <b-table-column label="Numéro telephone">
              <span v-for="phone in props.row.phones" :key="phone.number">
                {{ phone.number }}<br/>
              </span>
            </b-table-column>

            <!-- Email -->
            <b-table-column label="E-Mail">
              {{ props.row.email || '-' }}
            </b-table-column>
          </template>
        </b-table>
      </mu-paper>
    </div>
  </mu-dialog>
</template>

<script>
import moment from 'moment'

export default {
  name: 'pnr',
  props: ['open', 'dossier'],

  computed: {
    pnr () {
      if (this.dossier.pnr) {
        return this.dossier.pnr.new || this.dossier.pnr.original
      }
    }
  },

  methods: {
    flightDuration (flight) {
      const departureTime = moment(flight.departure.date, 'X')
      const arrivalTime = moment(flight.arrival.date, 'X')

      const diff = Math.abs(departureTime.diff(arrivalTime, 'minutes'))
      const hours = Math.floor(diff / 60)
      const minutes = diff % 60

      return `${String(hours).padStart(2, 0)}h ${String(minutes).padStart(2, 0)}m`
    },

    close () {
      this.$emit('update:open', false)
    }
  }
}
</script>



