<template>
  <mu-dialog :open="open" @close="close" title="Prix visa" v-if="destination">
    <div>
      <mu-switch
        label="Active"
        v-model="destination.active"
      />

      <br />

      <mu-text-field
        label="Nom"
        labelFloat
        v-model="destination.displayName"
      />

      <mu-text-field
        label="Lien"
        labelFloat
        v-model="destination.name"
      />

      <mu-text-field
        label="Image"
        labelFloat
        v-model="destination.image"
        type="url"
      />

      <br/>

      <fieldset>
        <legend>Vols :</legend>
        <div style="text-align: right">
          <mu-flat-button label="Ajouter vol" @click.prevent="addFlight()" v-if="$can('create', 'destinations')" />
        </div>

        <div v-for="(vol, index) in destination.vols" :key="index">
          <hr v-if="index > 0" />
          <mu-text-field
            label="Nom"
            labelFloat
            v-model="destination.vols[index].name"
          />

          <mu-text-field
            label="Prix"
            type="number"
            step="any"
            labelFloat
            v-model="destination.vols[index].price"
          />

          <br />

          <mu-text-field
            label="Image"
            type="url"
            labelFloat
            v-model="destination.vols[index].image"
          />

          <mu-text-field
            label="Lien"
            type="url"
            labelFloat
            v-model="destination.vols[index].link"
          />
        </div>
      </fieldset>

      <fieldset>
        <legend>Hotels :</legend>
        <div style="text-align: right">
          <mu-flat-button label="Ajouter hotel" @click.prevent="addHotel()" v-if="$can('create', 'destinations')" />
        </div>

        <div v-for="(hotel, index) in destination.hotels" :key="index">
          <hr v-if="index > 0" />
          <mu-text-field
            label="Nom"
            labelFloat
            v-model="destination.hotels[index].name"
          />

          <mu-text-field
            label="Prix"
            type="number"
            step="any"
            labelFloat
            v-model="destination.hotels[index].price"
          />

          <br />

          <mu-text-field
            label="Image"
            labelFloat
            v-model="destination.hotels[index].image"
          />

          <mu-text-field
            label="Lien"
            type="url"
            labelFloat
            v-model="destination.hotels[index].link"
          />
        </div>
      </fieldset>

      <fieldset>
        <legend>Infos :</legend>

        <div>
          <mu-select-field
            v-model="destination.infos.currency"
            label="Devise"
            labelFloat
            style="vertical-align: bottom"
          >
            <mu-menu-item value="euro" title="Euro" />
            <mu-menu-item value="dollar" title="Dollar" />
          </mu-select-field>

          <mu-text-field
            label="Code location"
            labelFloat
            v-model="destination.infos.locationCode"
          />

          <br />

          <mu-text-field
            label="Distance aéroport (km)"
            labelFloat
            type="number"
            v-model="destination.infos.airportDistance"
          />

          <mu-text-field
            label="Taux change (banque)"
            labelFloat
            type="number"
            step="any"
            v-model="destination.infos.exchangeRateBank"
          />

          <br />

          <mu-text-field
            label="Taux change (parallel)"
            labelFloat
            type="number"
            step="any"
            v-model="destination.infos.exchangeRateBlack"
          />
        </div>
      </fieldset>
    </div>

    <!-- Footer -->
    <footer>
      <div style="padding: 1rem 0; display:flex; flex-direction: row-reverse">
        <!-- Button annuler -->
        <mu-raised-button
          label="Fermer"
          @click.prevent="close"
        />

        <!-- Button sauvegarder -->
        <mu-raised-button
          label="Sauvegarder"
          @click.prevent="save"
          style="margin: 0 1rem"
          primary
        />
      </div>
    </footer>
  </mu-dialog>
</template>

<script>
import { cloneDeep } from 'lodash'

export default {
  name: 'edit-destination',
  props: ['open', 'data'],

  data () {
    return {
      destination: null
    }
  },

  watch: {
    open (value) {
      this.destination = cloneDeep(this.data)

      if (!this.destination.vols) {
        this.destination.vols = [{}]
      }

      if (!this.destination.hotels) {
        this.destination.hotels = [{}]
      }

      if (!this.destination.infos) {
        this.destination.infos = {}
      }
    }
  },

  methods: {
    close () {
      this.$emit('update:open', false)
    },

    async save () {
      this.$emit('update', this.destination)
      this.close()
    },

    addFlight () {
      this.destination.vols.push({})
      this.$forceUpdate()
    },

    addHotel () {
      this.destination.hotels.push({})
      this.$forceUpdate()
    }
  }
}
</script>

<style lang="scss" scoped>
fieldset {
  padding: 1rem;

  legend {
    padding: 0 .5rem;
  }
}
</style>



