<template>
  <div class="content-wrapper">
    <!-- Search field -->
    <div style="padding-bottom: 1.3rem">
      <mu-text-field
        icon="search"
        hintText="Chercher"
        v-model="searchQuery"
        fullWidth
      ></mu-text-field>
    </div>

    <!-- Tabs -->
    <div class="tabs is-boxed is-centered">
      <ul>
        <li
          v-for="(label, index) in statusHotels"
          :key="index"
          :class="{'is-active': status === index, 'is-disabled': $apollo.queries.hotels.loading}"
          @click="changeStatus(index)"
        >
          <a>
            {{ label }}
          </a>
        </li>
      </ul>
    </div>

    <b-table :data="hotelsFiltered" :loading="$apollo.queries.hotels.loading" :row-class="rowClass">
      <template slot-scope="props">
        <!-- ID -->
        <b-table-column field="_id" label="#">
          {{ props.row._id }}
        </b-table-column>

        <!-- BookingRef -->
        <b-table-column label="Booking ref.">
          <a @click.prevent="openDossierDialog(props.row)">
            {{ props.row.bookingRef || props.row.secondaryBookingRef }}
            ({{ props.row.bookingId }})
          </a>
        </b-table-column>

        <!-- Status partenaire -->
        <b-table-column field="status.booster.status" label="Status partenaire">
          <template v-if="props.row.secondaryBookingRef">
            <a @click="openHotelDialog(props.row)">{{ props.row.statusBooster.status }}</a>
          </template>
          <template v-else>{{ props.row.statusBooster.status }}</template>
        </b-table-column>

        <!-- Status -->
        <b-table-column field="status" label="Status" >
          <a @click.prevent="editStatus(props.row)" v-if="$can('update', 'hotels')">
            {{ statusHotels[props.row.status] }}
          </a>

          <span v-else>
            {{ statusHotels[props.row.status] }}
          </span>
        </b-table-column>

        <!-- Opérateur -->
        <b-table-column label="Op." :visible="$can('read', 'operateurs')">
          <a
            v-if="$can('update', 'operateurs')"
            @click.prevent="editOperator(props.row)"
          >{{ props.row.operator ? props.row.operator.name : '-' }}</a>

          <span v-else>{{ props.row.operator ? props.row.operator.name : '-' }}</span>
        </b-table-column>

        <!-- Contact -->
        <b-table-column field="contact" label="Contact" :visible="$can('read', 'contacts')">
          <a v-if="$can('update', 'contacts')" @click="editContacts(props.row)">
            {{ props.row.contacts.length ? props.row.contacts[0].name : '-' }} :
            <span style="white-space: nowrap">
              {{
                props.row.contacts.length
                ? (props.row.contacts[0].phones && props.row.contacts[0].phones.length
                  ? props.row.contacts[0].phones[0].number : '-')
                : '-'
              }}
            </span>
          </a>

          <span v-else>
            {{ props.row.contacts.length ? props.row.contacts[0].name : '-' }} :
            <span style="white-space: nowrap">
              {{
                props.row.contacts.length
                ? (props.row.contacts[0].phones && props.row.contacts[0].phones.length
                  ? props.row.contacts[0].phones[0].number : '-')
                : '-'
              }}
            </span>
          </span>
        </b-table-column>

        <!-- Date création -->
        <b-table-column field="createdDate" label="Date création">
          {{ formatDateTime(props.row.createdDate) }}
        </b-table-column>

        <!-- Cancelation deadline -->
        <b-table-column field="cancelDeadline" label="Option">
          {{ formatDateTime(props.row.cancelDeadline) }}
          <countdown
            v-if="deadlineTimeLeft(props.row.cancelDeadline)"
            :time="deadlineTimeLeft(props.row.cancelDeadline)"
            tag="p"
          >
            <template slot-scope="props">
              <b>{{ props.days }}:{{ props.hours }}:{{ props.minutes }}:{{ props.seconds }}</b>
            </template>
          </countdown>
        </b-table-column>

        <!-- Durée -->
        <b-table-column label="Dates/Durée">
          {{ formatDate(props.row.depDate) }} ->
          {{ formatDate(props.row.arrDate) }}
          <br/>
          ({{ travelDuration(props.row.depDate, props.row.arrDate) }})
        </b-table-column>

        <!-- Destination -->
        <b-table-column label="Destination">
          {{ props.row.city }} -
          {{ props.row.country }}
        </b-table-column>

        <!-- Prix -->
        <b-table-column field="price" label="Prix">
          <a @click="editTransactions(props.row)" v-if="$can('update', 'transactions')">
            <span v-if="props.row.transactions.length">
              {{ formatMoney(props.row.transactions[0].price.amount) }}
            </span>

            <span v-else>-</span>
          </a>

          <span v-else>
            <span v-if="props.row.transactions.length">
              {{ formatMoney(props.row.transactions[0].price.amount) }}
            </span>

            <span v-else>-</span>
          </span>
        </b-table-column>

        <!-- Actions -->
        <b-table-column label="Actions" :visible="!$can('operateurs', 'update')">
          <b-tooltip
            label="S'assigner le dossier"
            position="is-top"
            v-if="!$can('operateurs', 'update')"
          >
            <a
              class="button is-small is-rounded"
              style="width: 36px; height: 36px"
              @click.prevent="assignDossier(props.row)"
              :disabled="props.row.operator && $user._id === props.row.operator._id"
            >
              <mu-icon value="check" :size="24" />
            </a>
          </b-tooltip>
        </b-table-column>
      </template>
    </b-table>

    <!-- Pagination -->
    <b-pagination
      style="padding: 1.5rem 0"
      :total="hotels.total"
      :current.sync="page"
      :rounded="true"
      :per-page="pageSize"
      :order="'is-centered'"
    />

    <!-- Dossier dialog -->
    <dossier
      :open.sync="dossierDialog.open"
      :dossier="dossierDialog.dossier"
    />

    <!-- Edit status dialog -->
    <status
      :open.sync="statusDialog.open"
      :dossier="statusDialog.dossier"
      type="hotel"
      @update="updateDossier"
    />

    <!-- Edit operator dialog -->
    <operator
      :open.sync="operatorDialog.open"
      :dossier="operatorDialog.dossier"
      type="hotel"
      @update="updateDossier"
    />

    <!-- Edit contacts dialog -->
    <edit-contacts
      :open.sync="contactsDialog.open"
      :dossier="contactsDialog.dossier"
      :variables="variables"
      type="hotel"
    />

    <!-- Edit transactions dialog -->
    <edit-transactions
      :open.sync="transactionsDialog.open"
      :dossier="transactionsDialog.dossier"
      :variables="variables"
      :wilayas="wilayas"
      type="hotel"
    />

    <edit-hotel-dialog
      :open.sync="hotelWorldDialog.open"
      :dossier="hotelWorldDialog.dossier"
      :variables="variables"
    />
  </div>
</template>

<script>
import { findKey, orderBy } from 'lodash'
import moment from 'moment'
import VueCountdown from '@xkeshi/vue-countdown'

import Dossier from '@/components/hotels/Dossier'
import Status from '@/components/dossiers/Status'
import Operator from '@/components/dossiers/Operator'
import EditContacts from '@/components/edit-contacts'
import EditTransactions from '@/components/transactions/EditTransactions'
import editHotelDialog from '@/components/hotels/EditDialog'

import HOTELS_QUERY from '@/graphql/queries/hotelsQuery.gql'
import DOSSIER_HOTEL_MUTATION from '@/graphql/mutations/dossierHotel.gql'
import WILAYAS_QUERY from '@/graphql/queries/wilayasQuery.gql'

export default {
  name: 'hotels',
  components: {
    Dossier,
    Status,
    Operator,
    EditContacts,
    EditTransactions,
    editHotelDialog,
    countdown: VueCountdown
  },

  data () {
    return {
      searchQuery: '',

      dossierDialog: { open: false, dossier: {} },
      statusDialog: { open: false, dossier: {} },
      operatorDialog: { open: false, dossier: {} },
      contactsDialog: { open: false, dossier: {} },
      transactionsDialog: { open: false, dossier: {} },
      hotelWorldDialog: { open: false, dossier: {} },

      hotels: [],
      status: 'NEW',
      pageNumber: 1,
      pageSize: 5,

      wilayas: []
    }
  },

  computed: {
    statusHotels () { return this.$store.getters.status },

    page: {
      get () {
        return this.pageNumber
      },

      set (value) {
        const page = parseInt(value, 10)
        if (page <= 0) {
          this.pageNumber = 1
        } else {
          this.pageNumber = page
        }
      }
    },

    variables () {
      return {
        status: this.status,
        page: this.page,
        pageSize: this.pageSize
      }
    },

    hotelsFiltered () {
      if (this.hotels.data) {
        return orderBy(this.hotels.data.filter(el => {
          return el.status === this.status
        }), '_id')
      }

      return []
    }
  },

  apollo: {
    hotels: {
      query: HOTELS_QUERY,
      variables () {
        return { ...this.variables }
      },
      fetchPolicy: 'network-only'
    },

    wilayas: {
      query: WILAYAS_QUERY,
      fetchPolicy: 'network-only'
    }
  },

  methods: {
    openDossierDialog (dossier) { this.dossierDialog = { dossier, open: true } },
    editStatus (dossier) { this.statusDialog = { dossier, open: true } },
    editOperator (dossier) { this.operatorDialog = { dossier, open: true } },
    editContacts (dossier) { this.contactsDialog = { dossier, open: true } },
    editTransactions (dossier) { this.transactionsDialog = { dossier, open: true } },
    openHotelDialog (dossier) { this.hotelWorldDialog = { dossier, open: true } },

    rowClass (row, index) {
      if (!row.cancelDeadline) {
        return
      }

      const now = moment()
      const deadline = moment(row.cancelDeadline, 'X')
      const diff = deadline.diff(now, 'hours')

      if (diff < 24) {
        return 'is-danger'
      }

      if (diff > 24 && diff < 48) {
        return 'is-warning'
      }

      if (diff > 48) {
        return 'is-success'
      }
    },

    deadlineTimeLeft (deadline) {
      if (deadline) {
        const timeLeft = moment(deadline, 'X').diff(moment(), 'ms')

        if (timeLeft >= 0) {
          return timeLeft
        }

        return null
      }
    },

    changeStatus (status) {
      if (!this.$apollo.queries.hotels.loading) {
        this.status = status
      }
    },

    assignDossier (dossier) {
      if (dossier.operator) {
        if (this.$user._id === dossier.operator._id) return
      }

      this.$deleteModal.prompt({
        title: 'Assigner dossier',
        text: `Êtes-vous sûr de vouloir vous assigner ce dossier ?`,
        onConfirm: () => {
          this.updateDossier({
            _id: dossier._id,
            operator: this.$user._id
          })
        }
      })
    },

    async updateDossier (params) {
      try {
        await this.$apollo.mutate({
          mutation: DOSSIER_HOTEL_MUTATION,
          variables: params,
          update: (store, { data: { editDossierHotel } }) => {
            let data = store.readQuery({
              query: HOTELS_QUERY,
              variables: { ...this.variables }
            })
            const key = findKey(data.hotels.data, { _id: params._id })

            data.hotels.data[key] = {
              ...data.hotels.data[key],
              ...editDossierHotel
            }

            store.writeQuery({
              query: HOTELS_QUERY,
              variables: { ...this.variables },
              data
            })
          }
        })
        this.statusDialog.open = false
      } catch (error) {
        console.error(error)
        this.statusDialog.open = false
      }
    },

    travelDuration (arr, dep) {
      const arrivalDate = moment(arr, 'X')
      const departureDate = moment(dep, 'X')
      const travelDuration = Math.abs(arrivalDate.diff(departureDate, 'days'))

      return `${travelDuration + 1} jours / ${travelDuration} nuits`
    }
  }
}
</script>

<style lang="scss">
@import "~bulma/sass/utilities/initial-variables.sass";
@import "~bulma/sass/utilities/derived-variables.sass";

tr.is-danger {
  background-color: rgba($danger, .3);
}

tr.is-success {
  background-color: rgba($success, .3);
}

tr.is-warning {
  background-color: rgba($warning, .3);
}
</style>



