<template>
  <mu-dialog :open="open" :title="title" @close="close">
    <p style="padding: 1.3rem .5rem">
      {{ text || defaultText }}
    </p>
    <mu-flat-button label="Oui" slot="actions" secondary @click="confirm()"/>
    <mu-flat-button label="Non" slot="actions" primary @click="close"/>
  </mu-dialog>
</template>

<script>
export default {
  props: {
    title: String,
    text: String,
    onConfirm: {
      type: Function,
      default: () => {}
    }
  },

  data () {
    return {
      open: false,
      defaultText: 'Êtes-vous sûr de vouloir supprimer cet element ?'
    }
  },

  methods: {
    close () {
      this.open = false
      // Timeout for the animation complete before destroying
      setTimeout(() => {
        this.$destroy()
        if (typeof this.$el.remove !== 'undefined') {
          this.$el.remove()
        } else {
          this.$el.parentNode.removeChild(this.$el)
        }
      }, 150)
    },

    confirm () {
      this.onConfirm()
      this.close()
    }
  },

  mounted () {
    this.open = true
  }
}
</script>



